import actionConstants from '../constants/ActionConstants';

const initialState = {
    gameDetail: null,
    isUpdate: false,
  };
  
export default function score(state = initialState, action) {
    switch (action.type) {
      case actionConstants.UPDATE_SCORE:
        return {
          ...state,
          gameDetail : action.gameDetail,
          isUpdate: true
        };
        case actionConstants.RESET_SCORE:
          return{
              ...state,
              isUpdate : false
          }
      default:
        return state;
    }
}