import React, { Component } from  'react';
import {connect} from 'react-redux';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { isMobile } from 'react-device-detect';
import 'antd/dist/antd.css';
import './shopcarousel.scss';

class ShopCarousel extends Component {

    render() {
        const {players, player} = this.props;
        if (player.isSet) {
            let p = players.filter(val => val.player_id === `${player.pid}`);
            return (
                <>
                <Carousel autoPlay={true} autoplaySpeed={4000} showArrows={!isMobile} showThumbs={false} showStatus={false} infiniteLoop={true} >
                        {p && p.map(val => {
                            return (
                                <div className="shop-carousel-item-container-bg" key={val}>
                                    <div  className="shop-carousel-item-container">
                                        {/* <a href="#"> */}
                                            <div className="shop-carousel-item">
                                                <div className="shop-carousel-item-bg"></div>
                                                <div className="shop-carousel-item-img">
                                                    <img
                                                        src={val.leader_photo}
                                                        alt={val.first_name}
                                                        onLoad={() => {
                                                            
                                                        }}
                                                    />
                                                </div>
                                                    <div className="shop-carousel-item-name">{(val.first_name + " " + val.last_name)}</div>
                                                <div className="shop-carousel-item-desc">{val.view_text.toUpperCase()}</div>
                                            </div>
                                        {/* </a> */}
                                    </div>
                                </div>
                            );
                        })}
        
                    </Carousel>
                </>
                )
        } else {
            return (
                <>
                <Carousel autoPlay={true} autoplaySpeed={4000} showArrows={!isMobile} showThumbs={false} showStatus={false} infiniteLoop={true} >
                        {players && players.map(val => {
                            return (
                                <div className="shop-carousel-item-container-bg" key={val}>
                                    <div  className="shop-carousel-item-container">
                                        {/* <a href="#"> */}
                                            <div className="shop-carousel-item">
                                                <div className="shop-carousel-item-bg"></div>
                                                <div className="shop-carousel-item-img">
                                                    <img
                                                        src={val.leader_photo}
                                                        alt={val.first_name}
                                                        onLoad={() => {
                                                            
                                                        }}
                                                    />
                                                </div>
                                                    <div className="shop-carousel-item-name">{(val.first_name + " " + val.last_name)}</div>
                                                <div className="shop-carousel-item-desc">{val.view_text.toUpperCase()}</div>
                                            </div>
                                        {/* </a> */}
                                    </div>
                                </div>
                            );
                        })}
        
                    </Carousel>
                </>
                )
        }
    }
}

const mapStateToProps = (state) => {
    const { player } = state;
    return ({
        player
    })
}

export default connect(mapStateToProps)(ShopCarousel);