import React,{ useEffect, useState} from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import './style.scss'
import {Timeline, DataSet} from 'vis-timeline/standalone'
import moment from 'moment';
import { startTimer, stopTimer, updateTimer } from '../../redux/actions/updateTimer';
import { updateScore } from '../../redux/actions/updateScore';
import {updateEvent} from '../../redux/actions/updateEvent';
import { setGameDetail } from '../../redux/actions/getGameDetail';
import fetchGameDetail from '../../api/fetchGameDetail';
import fetchGameDetailPbp from '../../api/fetchGameDetailPbp';
import {useSpring, animated} from 'react-spring';
import { resetPlayersList, updatePlayersList } from '../../redux/actions/updatePlayers';
import { resetOPlayersList, updateOPlayersList } from '../../redux/actions/updateOPlayers';
var POSITIONS = ["C", "SF", "PF", "SG", "PG"];
function EventHistory (props) {
    const [gameDetail, setGameDetail] = useState(props.gameDetail.gameDetail);
    const [timeline, setTimeline] = useState(null);
    const [currentEvent, setCurrentEvent] = useState("");
    const [startTime, setStartTime] = useState(null);
    const [endTime, setEndTime] = useState(null);
    const [allEvents, setAllEvents] = useState([]);
    const [isLive , setLive] = useState(false);
    const [isTimelineEventsSet, setTimelineEvents] = useState(false);
    const [timerId, setTimerId] = useState(null);
    // const vwToPixel = value => `${(window.innerWidth * value) / 100}px`
    var allReplacedPlayersHistory = null;
    const timeLineLiveStyle = useSpring({
        from: {
            marginLeft: '0px',
        },
        to :  {
            marginLeft: '-75%'//vwToPixel(-35),
        }
    });

    const timelineBackStyle = useSpring({
        from: {
            marginLeft: '-75%'//vwToPixel(-35),
        },
        to : {
            marginLeft: '0px',
        }
    });

    const liveStyle = useSpring({
        from: {
            marginRight: '0%',
            width : '35%', 
            borderRadius : '10px 10px 10px 10px',
        },
        to : {
            marginRight: '0%',
            width : '150%',
            borderRadius : '10px 10px 10px 10px'
        }
    });

    const backStyle = useSpring({
        from: {
            margin: '25%',
            width : '150%',
        },
        to : {
            margin: '0%',
            width : '35%',
        }
    });

    useEffect(() => {
        if(timeline === null && props.gameDetail.isSuccess) {
            initTimeline();
        }
    }, [])

    useEffect(() => {
        if(timeline) {
            checkGameStatus();
            applyAxisCss();
            return() => {
                removeAllListeners();
            }
        }
    },[timeline]);

    useEffect(() => {
        if (isLive) {
            startTimer();
            if(timeline) {
                timeline.currentTime.stop();
            }
        } else {
            stopTimer();
            if(gameDetail && timeline) {
                fetchPbpGameDetails(1);
            }
        }
    },[isLive]);

    useEffect(() => {
        if (gameDetail) {
            getOpTeamPlayers();
            getSunsteamPlayers();
            props.updateEvent(gameDetail.g.lpla);
            props.updateTimer(gameDetail.g.cl);
            updateTimelineMaxValue();
        }
    },[gameDetail]);

    useEffect(() => {
        if (timeline) {
            updateItems();
        }
    }, [allEvents]);

    function applyAxisCss() {
        var axis = document.getElementsByClassName("vis-text vis-minor");   
        for (let i = 0; i < axis.length; i++) {
            if (axis[i].innerHTML === "12:00") {
                axis[i].style.color = "black";
            } else {
                axis[i].style.color = "white";
            }
        }
    }

    function updateItems() {
        var items = [];
        allEvents.forEach((events, index, array)=> {
            if (events) {
                var _items = events.map((event) => {
                    let start = startTime + minToSec(event.cl) * 1000 + index * 12 * 60 * 1000;
                    let end = start + 200;
                    var item = {
                        id: event.evt,
                        className: "event-history-timeline-item",
                        //content: event.de,
                        start:start,
                        end: end,
                        type:'range', //point, default -> box, range, background
                    }
                    return item;
                });
                items.push(..._items);
            }
        });

        timeline.setItems(items);
        if (!isTimelineEventsSet) {setTimelineEventsListeners()};
        allReplacedPlayersHistory = getReplacePlayersHistory(allEvents);

        if (isLive) {timeline.currentTime.stop()};
    }

    function checkGameStatus() {
        //check game is in progress or finished
        var {st} = gameDetail.g;
        fetchPbpGameDetails(1);
        if (st === 2) { //live game --> fetch pbp games from quart 1 to currnt quart;
            setLive(true);
        }
        console.log("checkgamestatus");
    }

    function updateTimelineMaxValue() {
        if (timeline && isLive) {
            let clock = minToSec(gameDetail.g.cl);
            let current = startTime + (gameDetail.g.p - 1) * 12 * 60 * 1000 + clock * 1000;
            timeline.setOptions({
                max: current + 5 * 60000
            });
            timeline.setCurrentTime(current);
            timeline.moveTo(current);
            timeline.currentTime.stop();
            setEndTime(current);

            if (timeline.itemsData.get(gameDetail.g.lpla.evt)) {
                return;
            }

            let start = startTime + minToSec(gameDetail.g.lpla.cl) * 1000 + (gameDetail.g.p - 1) * 12 * 60 * 1000;
            let end = start + 200;
            var item = {
                id: gameDetail.g.lpla.evt,
                className: "event-history-timeline-item",
                //content: event.de,
                start:start,
                end: end,
                type:'range', //point, default -> box, range, background
            }
            timeline.itemsData.add(item);
        }
    }

    function fetchFullGameDetail() { // call in 5 seconds interval for upate in live mode.
        fetchGameDetail(gameDetail.g.gid).then((data) => {
            const game = data.body
            setCurrentEvent(data.body.g.lpla.de);
            setGameDetail(game);
        }).catch((error) => {
            console.log(error);
        });
    }

    var events = [];
    function fetchPbpGameDetails(quart) {
        const {gid, p} = gameDetail.g;
        fetchGameDetailPbp(gid, quart).then((data) => {
            var _items = data.body.g.pla
            if(quart === 1) {
                events = [];
            }
            events.push(_items);
            if (quart < p) {
                fetchPbpGameDetails(quart + 1);
            } else {
                setAllEvents(events);
            }
            console.log(events);
        }).catch((error) => {
            console.log(error);
        })
    }

    function getReplacePlayersHistory(allevents) {
        let sunsReplaceHistory = []; //[time : 12:00, players :[{player},{...}]]
        let opReplaceHistory = [];
        const sunsTeamDetails = getSunsTeam(gameDetail);
        const oPteam = getOpTeam(gameDetail);
        let quart = 0;
        allevents.forEach(qEvents => {

            let qStartTime = startTime + quart * 12 * 60 * 1000;

            if (qEvents && qEvents.length > 0) {
                let sunsStartPlayers;
                sunsStartPlayers = getStartPlayersInQourt(qEvents, sunsTeamDetails.tid);
                let startPlayers = {time : qStartTime, players: sunsStartPlayers};
                sunsReplaceHistory.push(startPlayers);
                
                let opStartPlayers;
                opStartPlayers = getStartPlayersInQourt(qEvents, oPteam.tid);
                let oStartPlayers = { time: qStartTime, players: opStartPlayers };
                opReplaceHistory.push(oStartPlayers);
    
                qEvents.filter((e) => e.etype === 8) // suns team replace events only
                  .forEach((event) => {
                      const time = getStartTimeOfEvent(event, quart);
                    if (event.tid === sunsTeamDetails.tid) {
                        const currentPlayers = sunsReplaceHistory[sunsReplaceHistory.length - 1].players;
                        let newSunsPlayers = currentPlayers.map((p) => {
                            if (p.pid === event.pid) {
                                let newPlayer = getPlayerWithId(event.epid);
                                newPlayer.pos = p.pos;
                                return newPlayer;
                            } else return p;
                        });
                        if (sunsReplaceHistory[sunsReplaceHistory.length - 1].time === time) {
                            sunsReplaceHistory[sunsReplaceHistory.length - 1].players = newSunsPlayers;
                        } else {
                            sunsReplaceHistory.push({
                              time: getStartTimeOfEvent(event, quart),
                              players: newSunsPlayers,
                            });
                        }            
                    } else {
                        const currentPlayers = opReplaceHistory[opReplaceHistory.length - 1].players;
                        let newOPlayers = currentPlayers.map((p) => {
                            if (p.pid === event.pid) {
                                let newPlayer = getOPlayerWithId(event.epid);
                                newPlayer.pos = p.pos;
                                return newPlayer;
                            } else return p;
                        });
                        if (opReplaceHistory[opReplaceHistory.length - 1].time ===  time) {
                            opReplaceHistory[opReplaceHistory.length - 1].players = newOPlayers;
                        } else {
                          opReplaceHistory.push({
                            time: getStartTimeOfEvent(event, quart),
                            players: newOPlayers,
                          });
                        }
                    }
                });
            }
            quart += 1;
        });

        return {
            suns : sunsReplaceHistory,
            op : opReplaceHistory
        }
    }

    function getStartPlayersInQourt(qEvents, tid) {
        let startPlayersIds = [];
        let replacedIds = [];
        qEvents.filter(e => e.tid === tid && e.pid !== tid && e.pid !== 0).forEach(event => {
            if(event.etype === 8 && !replacedIds.includes(parseInt(event.epid))) {
                replacedIds.push(parseInt(event.epid));
            }

            if (!startPlayersIds.includes(event.pid) && !replacedIds.includes(event.pid)) {
                startPlayersIds.push(event.pid);
            }
        });
        let players = [];
        let pos = [...POSITIONS];
        const sunsTeamDetails = getSunsTeam(gameDetail);
        if (sunsTeamDetails.tid === tid) {
            startPlayersIds.forEach((id) => {
                let player = getPlayerWithId(id);
                players.push(player);
            });
        } else {
            startPlayersIds.forEach((id) => {
                let player = getOPlayerWithId(id);
                players.push(player);
            });
        }

        let player5 = players.filter(player => player);

        player5.forEach((player) => {
          if (player) {
            let i = pos.indexOf(player.pos);
            if (i > -1) pos.splice(i, 1);
          }
        });
        if (pos.length > 0) {
            for (let i = 0; i < player5.length; i++) {
              if (player5[i] && player5[i].pos === "") {
                player5[i].pos = pos[0];
                pos.splice(0, 1);
              }
            }
        }
        pos = [...POSITIONS];
        return player5;
    }

    function getCurrentActivePlayers(time) {
        const{op, suns }= allReplacedPlayersHistory;
        const sunsTeamDetails = getSunsTeam(gameDetail);
        const oPteam = getOpTeam(gameDetail);
        let sunsPlayers = [];
        let opPlayers = [];
        if (op[0].time > time) {
            opPlayers = op[0].players;
        } else if (op[op.length - 1].time < time) {
            opPlayers = op[op.length - 1].players;
        }

        if (suns[0].time > time) {
            sunsPlayers = suns[0].players;
        } else if (suns[suns.length - 1].time < time) {
            sunsPlayers = suns[suns.length - 1].players;
        }

        for (let i = 0; i < op.length - 1; i++) {
            if (op[i].time < time && time < op[i+1].time) {
                opPlayers = op[i].players;
            }
        }
        for (let i = 0; i < suns.length - 1; i++) {
            if (suns[i].time < time && time < suns[i+1].time) {
                sunsPlayers = suns[i].players;
            }
        }

        if (sunsPlayers.length < 5 && gameDetail.g.st === 2) {
            sunsPlayers = sunsTeamDetails.pstsg.filter(player => player.court === 1);
        }

        props.updateOPlayers(opPlayers, oPteam.tid);
        props.updatePlayers(sunsPlayers, sunsTeamDetails.tid);
    }

    function getSunsteamPlayers() {
        const sunsTeamDetails = getSunsTeam(gameDetail);
        props.updateScore(gameDetail);
        const players = sunsTeamDetails.pstsg.filter(player => player.court === 1);
        props.updatePlayers(players, sunsTeamDetails.tid);
    }

    function getOpTeamPlayers() {
        const oPteam = getOpTeam(gameDetail);
        const players = oPteam.pstsg.filter(player => player.court === 1);
        props.updateOPlayers(players, oPteam.tid);
    }

    function getPlayerWithId(pid) {
        const sunsTeamDetails = getSunsTeam(gameDetail);
        const players = sunsTeamDetails.pstsg.filter(player => player.pid === parseInt(pid));
        const copyPlayers = JSON.parse(JSON.stringify(players));
        if (copyPlayers && copyPlayers.length > 0) {
          return copyPlayers[0];
        } else {
          return null;
        }
    }

    function getOPlayerWithId(pid) {
        const opTeam = getOpTeam(gameDetail);
        const players = opTeam.pstsg.filter(player => player.pid === parseInt(pid));
        const copyPlayers = JSON.parse(JSON.stringify(players))
        if (copyPlayers && copyPlayers.length > 0) {
          return copyPlayers[0];
        } else {
          return null;
        }
    }

    function getSunsTeam(game) {

        const leftTeam = game.g.hls;
        const rightTeam = game.g.vls;
        let _sunsTeamDetails = leftTeam;
        if (rightTeam.tn === 'Suns') {
            _sunsTeamDetails = rightTeam;
        }
        return _sunsTeamDetails;
    }

    function getOpTeam(game) {
        const leftTeam = game.g.hls;
        const rightTeam = game.g.vls;
        let _opTeam = rightTeam;
        if (rightTeam.tn === 'Suns') {
            _opTeam = leftTeam;
        }
        return _opTeam;
    }

    function initTimeline() {
        const game = gameDetail.g;

        var container = document.getElementById("event-history-timeline");
        var items = new DataSet();
        let start = moment.utc(game.gdtutc + " " + game.utctm).toDate();
        var sTime = start.getTime();

        var majorLabels = (date, scale, step) => {
            switch (scale) {
                case 'milisecond':
                    return '';
                case 'second':
                    return '';
                case 'minute':
                    return '';
                default:
                    return '';
            }
        } 

        var minorLabels = (date, scale, step) => {
            switch (scale) {
                case 'milisecond':
                    return moment(date).format('sss') + 'ms';
                case 'second':
                    var sec = moment(date).format('s');
                    if (sec === '0') {
                        return '';
                    }
                    return sec + 's';
                case 'minute':
                    let diff = (moment(date).valueOf() - startTime) / 60000;
                    let cMin = diff % 12;
                    if (cMin < 0) cMin = -cMin;
                    else cMin = 12 - cMin;
                    if (cMin % 3 === 0) {
                        return cMin + ":00";
                    } else {
                        return '';
                    }
                case 'hour':
                    return moment(date).format('h:mm');
                default:
                    return '';
            }
        }

        let current;
        let max;
        if (game.st === 3) { // 1=pregame, 2=in progress, 3=complete
            current = sTime + game.p * 12 * 60 * 1000 // quart * 12 min = total period of timeline
            max = current + 5 * 60000;
        } else {
            let clock = minToSec(game.cl);
            current = sTime + (game.p - 1) * 12 * 60 * 1000 + clock * 1000;
            max = current + 5 * 60000;
        }
        var endEdgeTime = sTime + 5 * 60000;
        var options = {
            start: sTime - 5 * 60000,
            end: endEdgeTime,
            max: max,
            min: sTime - 5 * 60000,
            zoomMin: 100,
            zoomable: false,
            showMajorLabels: true,
            showMinorLabels: true,
            format: {majorLabels, minorLabels},
            maxHeight: "80px",
            height: "80px",
            margin: {
                item: 1,
                axis: 5
            },
            showCurrentTime: false,
            stack: false,
            orientation: {
                axis:"both",
            },
            timeAxis: {scale: 'minute', step: 1}
        };

        // create a Timeline
        let tl = new Timeline(container, items, null, options);

        if (game.st === 3) {
            tl.setCurrentTime(sTime);
            tl.moveTo(sTime);
        } else {
            tl.setCurrentTime(current);
            tl.moveTo(current);
        }

        setTimeline(tl);
        setStartTime(sTime);
        setEndTime(current);
    }

    function removeAllListeners() {
        if (timeline){
            timeline.currentTime.stop();
            timeline.off("rangechange", onTimelineScroll);
            timeline.off("currentTimeTick", onCurrentTimeTick);
            timeline.off("mouseDown", setMouseDown);
            timeline.off("mouseUp", setMouseUp);
            timeline.off("rangechanged", onTimelineScrollEnded);
            setAllEvents([]);
        }
    }

    function setTimelineEventsListeners() {

        timeline.on("rangechange", onTimelineScroll);
        timeline.on("currentTimeTick", onCurrentTimeTick);
        timeline.on("mouseDown", setMouseDown);
        timeline.on("mouseUp", setMouseUp);
        timeline.on("rangechanged", onTimelineScrollEnded);

        timeline.currentTime.start = function() {
            var me = this;
            function update () {
              me.stop();
              me.redraw();
              me.body.emitter.emit('currentTimeTick');
              // start a renderTimer to adjust for the new time
              me.currentTimeTimer = setTimeout(update, 100);
            }
        
            update();
        }
        timeline.setCurrentTime(startTime);
        timeline.setOptions({
            showCurrentTime: true
        });
    
        if (gameDetail.g.st !== '3') {
            timeline.setCurrentTime(endTime);
        }
        setTimelineEvents(true);
    }

    function setMouseDown(event) {
    }

    function setMouseUp(event) {
    }

    function onTimelineScroll(data) {
        if(data.byUser) {
            var start = data.start.getTime();
            var end = data.end.getTime();
            var mid = (start + end)/2;
            if (end < endTime + 5 * 60000) {
                timeline.setCurrentTime(mid);
                timeline.currentTime.start();
                setLive(false);
            } else {
                const {st} = gameDetail.g;
                timeline.setCurrentTime(mid);
                if (st === 2) {
                    setLive(true);
                    timeline.currentTime.stop()
                }
            }
        }
    }

    function onTimelineScrollEnded(data) {
        console.log(data);
        if (data.byUser && !isLive) {
            var start = data.start.getTime();
            var end = data.end.getTime();
            var mid = (start + end)/2;
            if (mid > startTime + 4 * 12 * 60 * 1000) {
                mid = startTime + 4 * 12 * 60 * 1000;
            }
            // let updateTime = timeline.getCurrentTime().getTime();

            let p = getQuartByTime(mid);
            let eventsP = allEvents[p];

            let countDown = startTime + 12 * (p + 1) * 60 * 1000 - mid;
            let clock = milisecondToclock(countDown);
            props.updateTimer(clock);

            if (eventsP) {
                let currentEvents = eventsP.filter(function(event){
                    let estart = getStartTimeOfEvent(event, p);
                    if (estart <= mid && start <= estart) {
                    return event;
                    }
                    return null;
                });

                if (currentEvents && currentEvents.length > 0) {
                    let lastEvent = currentEvents[currentEvents.length - 1];
                    let totalEvents = lastEvent.de ? lastEvent.de : "";
                
                if (totalEvents === "") return;
                setCurrentEvent(totalEvents);

                const {
                    hls: leftTeam, vls: rightTeam, gdtutc, utctm,
                    } = gameDetail.g;
                
                let scoreDetail = { g: {
                        p:p + 1, cl:lastEvent.cl ,
                        hls: {tn : leftTeam.tn, s: lastEvent.hs}, 
                        vls: {tn : rightTeam.tn, s: lastEvent.vs},
                        gdtutc : gdtutc, 
                        utctm : utctm
                    }
                }

                    getCurrentActivePlayers(mid);
                    props.updateEvent(lastEvent);
                    props.updateScore(scoreDetail);
                }
            }
        }
    }

    function getQuartByTime(time) {
        if (time <= startTime + 12 * 60 * 1000) {
            return 0;
        } else if (startTime + 12 * 60 * 1000 < time && time <= startTime + 2 * 12 * 60 * 1000){
            return 1;
        } else if (startTime + 2 * 12 * 60 * 1000 < time && time <= startTime + 3 * 12 * 60 * 1000) {
            return 2;
        } else if (startTime + 3 * 12 * 60 * 1000 < time && time <= startTime + 4 * 12 * 60 * 1000) {
            return 3;
        } else {
            return -1;
        }
    }
    
    var countTimer = 5;
    function onCurrentTimeTick(options) {

        applyAxisCss();
         
        if (timeline.getCurrentTime().getTime() >= endTime) {
            timeline.currentTime.stop();
            var mid = (timeline.range.start + timeline.range.end)/2;
            timeline.setCurrentTime(mid);
            return;
        }
        let updateTime = timeline.getCurrentTime().getTime();
        timeline.moveTo(updateTime);

        let p = getQuartByTime(updateTime);
        let eventsP = allEvents[p];
        countTimer -= 1;
        if (countTimer <= 0) {
            let countDown = startTime + 12 * (p + 1) * 60 * 1000 - updateTime;
            let clock = milisecondToclock(countDown);
            props.updateTimer(clock); //moment(countDown).format("mm:ss")
            countTimer = 5;
        }

        if (eventsP) {
            let currentEvents = eventsP.filter(function(event){
                let start = getStartTimeOfEvent(event, p);
                if (start <= updateTime && updateTime <= start + 1000) {
                  return event;
                }
                return null;
            });

            let totalEvents = "";
            let lastEvent = null;
            currentEvents.forEach(element => {
                if (totalEvents === "") totalEvents = element.de;
                else totalEvents += " " + element.de;
                lastEvent =  element;

                // if (element.etype === 8) {
                //     replacePlayer(element.pid, element.epid, element);
                // }
                
            });
            
            if (totalEvents === "") return;
            setCurrentEvent(totalEvents);

            const {
                hls: leftTeam, vls: rightTeam, gdtutc, utctm,
                } = gameDetail.g;
            
            let scoreDetail = { g: {
                    p:p + 1, cl:lastEvent.cl ,
                    hls: {tn : leftTeam.tn, s: lastEvent.hs}, 
                    vls: {tn : rightTeam.tn, s: lastEvent.vs},
                    gdtutc : gdtutc, 
                    utctm : utctm
                }
            }

            getCurrentActivePlayers(updateTime);
            props.updateEvent(lastEvent);
            props.updateScore(scoreDetail);
        }
    }

    function onLiveClick() {
        const {st} = gameDetail.g;
        if (!isLive && st === 2) {
            setLive(true);
        }
    }

    function getStartTimeOfEvent(event, quart) {
        let eventTimeSec = minToSec(event.cl);
        return startTime + eventTimeSec * 1000 + quart * 12 * 60 * 1000;
    }

    function minToSec(min) { //mm:ss
        let tObj = moment(min, 'mm:ss').toObject();
        let s = tObj.minutes * 60 + tObj.seconds;
        let second = 720 - s; // total quart period : 720, count down from 720..
        return second;
    }

    function milisecondToclock(ms) {
        let sec = Math.floor(ms / 1000);
        let min = Math.floor(sec/60);
        let rsec = sec % 60;

        let minutes = "";
        let seconds = "";
        if (min < 10) {
            minutes = `0${min}`;
        } else {
            minutes = `${min}`;
        }
        if (rsec < 10) {
            seconds = `0${rsec}`;
        } else {
            seconds = `${rsec}`;
        }

        return `${minutes}:${seconds}`;
    }

    function startTimer() {
        fetchFullGameDetail();
        let timer = setInterval(()=> {
            fetchFullGameDetail();
        }, 5000)
        setTimerId(timer);
    }

    function stopTimer() {
        clearInterval(timerId);
    }

    function renderHistory() {
        return (<div className="event-history-current">{currentEvent && currentEvent.toUpperCase()}</div>);
    }
    return(
            <div className="event-history">
                {gameDetail && renderHistory()}
                <div className="event-history-timeline-container" >
                    <animated.div id="event-history-timeline" className="event-history-timeline" style={isLive ? timeLineLiveStyle : timelineBackStyle}/>
                    <animated.div id="event-history-live" className="event-history-live" onClick={onLiveClick} style={isLive ? liveStyle : backStyle}>
                        {isLive && <div className="event-history-live-red"> <div className="event-history-live-red-dot"/> </div>}
                        <div className="event-history-live-txt">Live</div>
                    </animated.div>
                </div>
            </div>
        );
}

EventHistory.propTypes = {
    stopTimer: PropTypes.func.isRequired,
    startTimer: PropTypes.func.isRequired,
    updateTimer: PropTypes.func.isRequired,
    updateScore: PropTypes.func.isRequired,
    updateEvent: PropTypes.func.isRequired,
    setGameDetail: PropTypes.func.isRequired,
    updatePlayers: PropTypes.func.isRequired,
    gameDetail: PropTypes.object.isRequired,
    timer: PropTypes.object.isRequired,
  };

const mapStateToProps = (state) => {
    const { gameDetail, timer, players, oPlayers} = state;
    return{
        gameDetail: gameDetail,
        players: players,
        timer: timer,
        oPlayers: oPlayers,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        stopTimer: ()=>dispatch(stopTimer()),
        startTimer: (timer) => dispatch(startTimer(timer)),
        updateTimer: (time) => dispatch(updateTimer(time)),
        updateScore: (score) => dispatch(updateScore(score)),
        updateEvent: (event) => dispatch(updateEvent(event)),
        setGameDetail: (game) => dispatch(setGameDetail(game)),
        updatePlayers : (players, teamId) => dispatch(updatePlayersList(players, teamId)),
        resetPlayers : () => dispatch(resetPlayersList()),
        updateOPlayers: (players, teamId) => dispatch(updateOPlayersList(players, teamId)),
        resetOPlayers: () => dispatch(resetOPlayersList()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EventHistory);