import React from 'react'
import { connect } from 'react-redux';
import STPlayer from '../../components/Player/STPlayer';

function STPlayerList(props) {

    const {shottracker} = props;

    function renderPlayers() {
        const {point_guard, shooting_guard, center, small_forward, power_forward} = shottracker.shottracker;
          return (
              <>
              <STPlayer key={point_guard.player_id}  details={point_guard} pos="PG"/>
              <STPlayer key={shooting_guard.player_id} details={shooting_guard} pos="SG"/>
              <STPlayer key={center.player_id} details={center} pos="C"/>
              <STPlayer key={small_forward.player_id} details={small_forward} pos="SF"/>
              <STPlayer key={power_forward.player_id} details={power_forward} pos="PF"/>
              </>
          );
    }

    return (
        <>
            {shottracker && renderPlayers()}
        </>
    );

}

const mapStateToProps = (state) => {
    const {shottracker} = state;
    return {
        shottracker : shottracker,
    }
}
 
export default connect(mapStateToProps)(STPlayerList);