import React, { useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';
import { injectIntl } from 'react-intl';
import {connect} from 'react-redux';
import routes from '../../common/constants/routes';
import 'antd/dist/antd.css';
import './app.scss';
import Header from '../../components/Header/Header';
import OnCourt from '../OnCourt';
import Shop from '../Shop';
import Social from '../Social';
// import {SocketContext, socket} from '../../socket/socket';
import {setGA4, resetGA4} from '../../redux/actions/updateGA4';
import GA4React from 'ga-4-react';

function App(props) {
  
  useEffect(() => {
    const ga4react = new GA4React('G-CZQ8XWX6KL');
    ga4react.initialize().then((ga4) => {
      props.setGA4(ga4);
    }, (err) => {
      console.log(err);
      props.resetGA4();
    });
    return ()=> {
      props.resetGA4();
    }
  })

  return (
    // <SocketContext.Provider value={socket}>
    <div className="App">
      <div className="app-wrap">
          <Switch>
            <>
              <Route
                exact
                path={routes.default}
                component={() => (
                  <>
                    <Header />
                    <OnCourt />
                  </>
                )}
              />
              <Route
                path={routes.swagShop}
                component={() => (
                  <>
                    <Header />
                    <Shop />
                  </>
                )}
              />
              <Route
                path={routes.social}
                component={() => (
                  <>
                    <Header />
                    <Social />
                  </>
                )}
              />
            </>
          </Switch>
      </div>
    </div>
    // </SocketContext.Provider>
  );
}

const mapDispatchToProps = (dispatch) => {
  return {
    setGA4: (ga4) => dispatch(setGA4(ga4)),
    resetGA4: () => dispatch(resetGA4())
  }
}

export default connect(null, mapDispatchToProps)(injectIntl(App));
