import React from 'react'
import { connect } from 'react-redux';
import { sortPlayers } from '../../common/helpers/commonUtils';
import Player from '../../components/Player';

function PlayerList(props) {

    const {playersDetail} = props;

    function renderPlayers() {
        const {players, teamId} = playersDetail;

        const _players = sortPlayers(players).map((player, index) => {
          return (
              <Player key={player.pid} teamId={teamId} details={player} index={index}/>
          );
        });
        return _players;
    }

    return (
        <>
            {playersDetail.players && renderPlayers()}
        </>
    );

}

const mapStateToProps = (state) => {
    const {players} = state;
    return {
        playersDetail : players,
    }
}
 
export default connect(mapStateToProps)(PlayerList);