import actionConstants from '../constants/ActionConstants';

const initialState = {
    data: null,
    isUpdate: false
  };
  
export default function liveData(state = initialState, action) {
    switch (action.type) {
      case actionConstants.UPDATE_LIVE_DATA:
        return {
          ...state,
          data : action.data,
          isUpdate: true
        };
      case actionConstants.RESET_LIVE_DATA:
        return{
            ...state,
            isUpdate : false
        }
      default:
        return state;
    }
}