import actionConstants from '../constants/ActionConstants';

const initialState = {
    page: null,
    visitTime: null,
    isSet: false,
  };
  
export default function currentPage(state = initialState, action) {
    switch (action.type) {
      case actionConstants.SET_CURRENT_PAGE:
        return {
          ...state,
          page : action.page,
          visitTime: action.time,
          isSet: true
        };
      case actionConstants.RESET_CURRENT_PAGE:
        return{
            ...state,
            isSet : false
        }
      default:
        return state;
    }
}