import actionConstants from '../constants/ActionConstants';

const initialState = {
    ga4: null,
    initialized: false,
  };
  
export default function ga4(state = initialState, action) {
    switch (action.type) {
      case actionConstants.SET_GA4:
        return {
          ...state,
          ga4 : action.ga4,
          initialized: true
        };
      case actionConstants.RESET_GA4:
        return{
            ...state,
            initialized : false
        }
      default:
        return state;
    }
}