import React, { useEffect, useLayoutEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import { getTeamSchedule, resetTeamSchedule } from '../../redux/actions/getTeamSchedule';
import { getGameDetail, resetGameDetail } from '../../redux/actions/getGameDetail';
import CourtView from '../../components/CourtView';
import ScoreView from '../../components/ScoreView'
import PlayerList from '../../components/PlayerList';
import EventHistory from '../../components/EventHistory';
import {ActivityIndicator} from 'antd-mobile';
import {getShotTracker, resetShotTracker} from '../../redux/actions/getShotTracker';
import './oncourt.scss';
import { updateLiveData } from '../../redux/actions/updateLiveData';
import getSubscribeToken from '../../api/getSubscribeToken';
import {updateSubscribe} from '../../redux/actions/updateSubscribeToken';
import STCourtView from '../../components/CourtView/STCourtView';
import STScoreView from '../../components/ScoreView/STScoreView';
import STPlayerList from '../../components/PlayerList/STPlayerList';
import moment from 'moment';
import { updateScore } from '../../redux/actions/updateScore';

function OnCourt(props) {

    const { teamSchedule, gameDetail , shottracker} = props;

    var visitTime = new Date();
    function checkSunsSchedule() {
        const games = teamSchedule.teamSchedule.gscd.g;
        
        var pastGames = [];
        var futureGAmes = [];
        var game;
        games.forEach(element => {
            var difDate = compareGameDate(element);
            if (difDate === 0) {
                game = element;
            } else if (difDate < 0) {
                pastGames.push(element);
            } else {
                futureGAmes.push(element);
            }
        });
        // if (!game && (futureGAmes.length > 0)) {  // if future game is exist
        //     game = futureGAmes[0];
        // }
        if (!game) {
            game = pastGames[pastGames.length - 1]; // if future game is not exist then get latest game in the past.
        }
        
        let selectedGame = game.gid;
        props.getGameDetail(selectedGame);
    }

    function compareGameDate(game) {
        if (game.gdtutc === 'TBD' || game.utctm === 'TBD') {
            return 1;
        }
        var toDay = moment.utc().toDate();
        var gameDate = moment.utc(game.gdtutc + " " + game.utctm).toDate();
        if (toDay === gameDate) {
            return 0;
        } else if (toDay < gameDate) {
            return 1;
        } else {
            return -1;
        }
    }

    useEffect(() => {
        if (gameDetail.isSuccess) {
            props.resetGameDetail();
        }
    }, [gameDetail.isSuccess]);

    useEffect(() => {
        if (teamSchedule.isSuccess) {
            props.resetTeamSchedule();
            checkSunsSchedule();
        }
    }, [teamSchedule.isSuccess]);

    useEffect(() => {
        if (props.ga4.initialized) {
            props.ga4.ga4.pageview('OnCourt');
            props.ga4.ga4.gtag('event', 'visit_page', {'page': 'OnCourt'});
        }

        setupBeforeUnloadListener();

        return () => {
            if (props.ga4.initialized) {
                let current = new Date();
                let duration = current - visitTime
                props.ga4.ga4.gtag('event', 'leave_page', {'page': 'OnCourt', 'duration': duration});
                removeBeforeUnloadListener();
            }
        }
    }, []);

    useLayoutEffect(() => {
        props.getTeamSchedule();
        props.getShotTracker();
    }, []);

    useLayoutEffect(() => {
        if (shottracker.isSuccess && shottracker.shottracker.enable) {
            getSubscribeToken("game-123").then(response => {
                console.log(response.body);
                let url = `ws://mocklive.shottracker.com:8081/?token=${response.body.token}`;
                establishWebsocketConnection(url);
                props.updateSubscribe(response.body);
            });
        }
    }, [shottracker]);

    function establishWebsocketConnection(url) {
        console.log(`Connecting to ${url}`);
        let wsConnection = new WebSocket(url);
        wsConnection.addEventListener('open', function(event){
          sendPing(this);
        });
        wsConnection.addEventListener('message', function(payload){
          props.updateLiveData(payload.data);
        });
    }
      
    function sendPing(conn) {
        setInterval(function(){
            conn.send(JSON.stringify({action: 'ping'}));
        },60000);
    }

    function reportBeforeUnload() {
        if (props.ga4.initialized) {
            let current = new Date();
            let duration = current - visitTime
            props.ga4.ga4.gtag('event', 'leave_page', {'page': 'OnCourt', 'duration': duration});
        }
    }
    
    function setupBeforeUnloadListener() {
        window.addEventListener("beforeunload", onBeforeUnload);
    };

    function removeBeforeUnloadListener() {
        window.removeEventListener("beforeunload", onBeforeUnload);
    }

    function onBeforeUnload(ev) {
        ev.preventDefault();
        return reportBeforeUnload();
    }

    function renderCourt() {
        if (gameDetail.gameDetail && gameDetail.gameDetail.g.st === "1") {

            return (
                <div className="time_left">
                </div>
            )
        } else {
            return (<CourtView/>)
        }
    }

    function renderPlayers() {
        if (gameDetail.gameDetail && gameDetail.gameDetail.g.st !== "1") {
            return (<PlayerList/>)
        }
    }

    function renderHistory() {
        if (gameDetail.gameDetail && gameDetail.gameDetail.g.st !== "1") {
            return (<EventHistory/>)
        }
    }

    return (
            <>
            {!(gameDetail.isFetching || teamSchedule.isFetching || shottracker.isFetching)
                && (
                <div className="container" id="container">
                    {shottracker.shottracker.enable ? <STScoreView /> : <ScoreView />}
                    <div id="playground-wrap" className="playground-wrap" >
                    {shottracker.shottracker.enable ? <STCourtView /> : renderCourt()}
                    </div>
                    <div className="players-wrap">
                    {shottracker.shottracker.enable ? <STPlayerList/> : renderPlayers()}
                    </div>
                    {!shottracker.shottracker.enable && renderHistory()}
                </div>
                )}
                {(gameDetail.isFetching || teamSchedule.isFetching) && (
                    <div className="indicator-container">
                        <ActivityIndicator color="blue" size="large" text=""/>
                    </div>
                )}
            </>
    );
}

const mapStateToProps = (state) => {
    const { teamSchedule, gameDetail, ga4, shottracker, subscribeToken} = state;
    return ({
        teamSchedule,
        gameDetail,
        ga4,
        shottracker,
        subscribe: subscribeToken
    });
  };
  
  OnCourt.propTypes = {
    getTeamSchedule: PropTypes.func.isRequired,
    resetTeamSchedule: PropTypes.func.isRequired,
    getGameDetail: PropTypes.func.isRequired,
    resetGameDetail: PropTypes.func.isRequired,
    teamSchedule: PropTypes.object.isRequired,
    gameDetail: PropTypes.object.isRequired,
  };
  
  export default connect(mapStateToProps, {
    getTeamSchedule,
    resetTeamSchedule,
    getGameDetail,
    resetGameDetail,
    getShotTracker,
    resetShotTracker,
    updateLiveData,
    updateSubscribe,
    updateScore
  })(injectIntl(OnCourt));