import actionConstants from '../constants/ActionConstants';

const initialState = {
    isUpdate: false,
    player: null,
  };

  export default function shopPlayer(state = initialState, action) {
    switch (action.type) {
        case actionConstants.UPDATE_SHOP_PLAYER:
            return {
            ...state,
            isPlaying: true,
            player: action.player,
            };
        case actionConstants.STOP_TIMER:
            return{
                ...state,
                isPlaying : false,
            }
        default:
          return state;
      }
    
}