/**
 * Central action dispatcher
 * @param {function} serviceMethod
 * @param {string} actionTypeSuccess
 * @param {string} actionTypeFailure
 * @param {string} actionTypeInProgress
 * @param {object} extra
 * @param {function} callback
 */
function dispatchResponseToReducers(serviceMethod, actionTypeSuccess,
  actionTypeFailure, actionTypeInProgress, extra, callback) {
  return (dispatch) => {
    // dispatch action for in progress
    dispatch({
      type: actionTypeInProgress,
      extra,
    });
    serviceMethod()
      .then((response) => {
        const {
          error, body, headers, status, statusText,
        } = response;
        if (error) { // dispatch failure action with error code if the service method fails
          dispatch({
            type: actionTypeFailure,
            code: status,
            message: statusText,
            extra,
            headers,
          });
          if (callback) callback(headers, response);
        } else if (body) { // dispatch success action with payload if service method succeeds
          dispatch({
            type: actionTypeSuccess,
            payload: body,
            message: statusText,
            extra,
            headers,
          });
          if (callback) callback(headers, response);
        }
      }).catch((error) => { // handles error cases for service method
        if (error.status) {
          dispatch({
            type: actionTypeFailure,
            code: error.status,
            message: error.response.body.message || error.message,
            extra,
          });
        } else {
          dispatch({
            type: actionTypeFailure,
            code: 'NETWORK_ERROR',
            message: "We're sorry, we've experienced an error. Please quit the operation and try again. If the error persists please contact us for assistance.",
            extra,
          });
        }
        if (callback) callback(error.status, error.message);
      });
    return '';
  };
}

export default dispatchResponseToReducers;
