import actionConstants from '../constants/ActionConstants';

const initialState = {
  error: '',
  gameDetail: [],
  isFetching: false,
  isSuccess: false,
};

export default function gameDetailPbp(state = initialState, action) {
  switch (action.type) {
    case actionConstants.GET_GAME_DETAIL_PBP_REQUEST:
      return {
        ...state,
        error: '',
        isFetching: true,
        isSuccess: false,
      };
    case actionConstants.GET_GAME_DETAIL_PBP_SUCCESS:
      return {
        ...state,
        gameDetail: action.payload,
        isFetching: false,
        isSuccess: true,
      };
    case actionConstants.GET_GAME_DETAIL_PBP_FAILURE:
      return {
        ...state,
        error: action.message,
        isFetching: false,
      };
    case actionConstants.GET_GAME_DETAIL_PBP_RESET:
      return {
        ...state,
        error: '',
        isSuccess: false,
        isFetching: false,
      };
    case actionConstants.RESET:
      return {
        ...initialState,
      };
    default:
      return state;
  }
}