import actionConstants from '../constants/ActionConstants';

const initialState = {
    players: null,
    isUpdate: false,
    teamId:0
  };
  
export default function oPlayers(state = initialState, action) {
    switch (action.type) {
      case actionConstants.UPDATE_O_PLAYERS_LIST:
        return {
          ...state,
          players : action.players,
          teamId : action.teamId,
          isUpdate: true
        };
      case actionConstants.RESET_O_PLAYERS_LIST:
        return{
            ...state,
            isUpdate : false
        }
      default:
        return state;
    }
}