import React, {useState, useEffect} from 'react'
import { connect } from 'react-redux';
import './scoreview.scss';


function STScoreView (props){

    // const [state0, setState0] = useState(null);
    // const [state1, setState1] = useState(null);
    // const [shots0, setShots0] = useState(null);
    // const [shots1, setShots1] = useState(null);
    const [clock, setClock] = useState(null);
    // const [marker, setMarker] = useState(null);

    useEffect(()=> {
      if (props.liveData.isUpdate) {
        updateLiveEvent(props.liveData.data);
      }
    },[props.liveData])

    function updateLiveEvent(liveData) {
      if (liveData.type === "stats") {
        if (liveData.data.team_id === props.subscribe.response.teams[0].id) {
          // setState0(liveData.data);
        } else if (liveData.data.team_id === props.subscribe.response.teams[1].id) {
          // setState1(liveData.data);
        }
      } else if (liveData.type === "shot") {
        if (liveData.data.team_id === props.subscribe.response.teams[0].id) {
          // setShots0(liveData.data);
        } else if (liveData.data.team_id === props.subscribe.response.teams[1].id) {
          // setShots1(liveData.data);
        }
      } else if (liveData.type === "marker") {
        // setMarker(liveData.data);
      } else if (liveData === "clock") {
        setClock(liveData.data);
      }
  }

    function renderContent() {
        const {
            teams
          } = props.subscribe.response;
        return (
            <>
            <div className="rivals-timer" >{teams[0].name + " VS " + teams[1].name }</div>
            <div className="rivals-wrap">
              <div className="rival">
                <div className="rival-team-or-quarter">{teams[0].name.toUpperCase()}</div>
                <div className="rival-team-or-quarter-value">{}</div>
              </div>
              <div className="rival">
                <div className="rival-team-or-quarter quarter">{clock ? clock.period : "Q1"}</div>
                <div className="rival-team-or-quarter-value quarter">{clock ? clock.game_clock.min + ":" + clock.game_clock.sec : "0:00"}</div>
              </div>
              <div className="rival">
                <div className="rival-team-or-quarter right-team">{"SUNS"}</div>
                <div className="rival-team-or-quarter-value right-team">{}</div>
              </div>
            </div>
            </>
          );
    }

    function render(){
        
        return (
            <>
            {props.liveData.isUpdate && renderContent()}
            </>
        )
    }

    return(
        render()
    )
}

const mapStateToProps = (state) => {
  const { liveData, subscribeToken} = state;
  return{
      liveData: liveData,
      subscribe: subscribeToken,
  }
}

export default connect(mapStateToProps)(STScoreView);