import React, { useEffect, useState } from 'react';
import SocialFeedList from '../../components/SocialFeedList';
import fetchSeasonPlayers from '../../api/fetchSeasonPlayers';
import {ActivityIndicator, Toast} from 'antd-mobile';
import './social.scss';
import { connect } from 'react-redux';
import {resetPlayer} from '../../redux/actions/updatePlayer';

function Social(props) {

    const [loaded, setLoaded] = useState(false);
    const [players, setPlayers] = useState(null);
    const [visitTime] = useState(new Date());
    const [notified, setNotified] = useState(true);

    useEffect(() => {
        if (!loaded) {
            fetchPlayers();
            setLoaded(true);
        }
    }, [loaded]);

    useEffect(() => {
        if (props.ga4.initialized) {
            props.ga4.ga4.pageview('Social');
            props.ga4.ga4.gtag('event', 'visit_page', {'page': 'Social'});
        }
        setupBeforeUnloadListener();
        showToast()
        return () => {
            if (props.ga4.initialized) {
                let current = new Date();
                let duration = current - visitTime;
                props.ga4.ga4.gtag('event', 'leave_page', {'page': 'Social', 'duration': duration});
                removeBeforeUnloadListener();
            }

            if (props.player.isSet) {
                props.resetPlayer();
            }
        }
    },[]);

    function reportBeforeUnload() {
        if (props.ga4.initialized) {
            let current = new Date();
            let duration = current - visitTime;
            props.ga4.ga4.gtag('event', 'leave_page', {'page': 'Social', 'duration': duration});
        }
    }
    
    function setupBeforeUnloadListener() {
        window.addEventListener("beforeunload", onBeforeUnload);
    };

    function removeBeforeUnloadListener() {
        window.removeEventListener("beforeunload", onBeforeUnload);
    }

    function onBeforeUnload(ev) {
        ev.preventDefault();
        return reportBeforeUnload();
    }

    function fetchPlayers() {
        fetchSeasonPlayers('2020').then((data) => {
            console.log(data.body);
            setPlayers(data.body);
        });
    };

    function showToast() {
        if (props.player.isSet) {

            setTimeout(() => {
                setNotified(false)
            }, 2000)

            setTimeout(() => {
                setNotified(true)
            }, 5000)
        }
    }

    return(
        <>
        {loaded && players &&
            <div className="container">
                {props.player.isSet && !notified && <div className="shoplist-notify">Social media related to {props.player.name} has been loaded successfully!</div>}
                <SocialFeedList players={players}/>
            </div>
        }
        {!loaded && 
            <div className="indicator-container">
                <ActivityIndicator color="blue" size="large" text=""/>
            </div>
        }
        </>
    );
}

const mapStateToProps = (state) => {
    const { ga4, player} = state;
    return ({
        ga4,
        player
    });
  };

  const mapDispatchToProps = (dispatch) => {
    return {
      resetPlayer: () => dispatch(resetPlayer())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Social);