import React, { Component } from 'react';
import 'antd/dist/antd.css';
import './shoplist.scss';
import { List, Spin } from 'antd';
import {Button, WingBlank, Toast} from 'antd-mobile';
import {updateARViewProduct} from '../../redux/actions/updateARViewProduct';
// import InfiniteScroll from 'react-infinite-scroller';
import { connect } from 'react-redux';
import ARView from '../ARView';
import {fetchShopProductsLimit, fetchPlayerProductsLimit} from '../../api/fetchShopProducts';

class ShopList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            products: [],
            hasMore: true,
            loading: false,
            page: 0,
            modelLoading: true,
            notified: true, 
        }
        this.onViewInAR = this.onViewInAR.bind(this);
        this.onBuyNow = this.onBuyNow.bind(this);
    }

    componentDidMount() {
        if (this.props.player.isSet) {
            fetchPlayerProductsLimit(this.props.player.pid, 0, 3).then((data) => {
                if (data.body) {
                    this.setState({
                        products: data.body
                    });
                    
                    setTimeout(() => {
                        this.setState({
                            notified: false
                        })
                    }, 1500)
                    
                    setTimeout(()=> {
                        this.setState({
                            notified: true
                        })  
                    }, 5500)
                }
            })
        } else {
            fetchShopProductsLimit(0, 3).then((data) => {
                this.setState({
                    products: data.body
                });
            })
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.page < this.state.page) {
            this.fetchProducts(this.state.page);
        }
    }

    fetchProducts(page) {

        if (this.props.player.isSet) {
            fetchPlayerProductsLimit(this.props.player.pid, page, 3).then((data) => {
                let products = this.state.products.concat(data.body)
                this.setState({
                    products: products,
                    loading: false,
                    modelLoading: true,
                    hasMore: !(data.body.length === 0),
                });
                window.dispatchEvent(new Event('resize'));
            })
        } else {
            fetchShopProductsLimit(page, 3).then((data) => {
                let products = this.state.products.concat(data.body)
                this.setState({
                    products: products,
                    loading: false,
                    modelLoading: true,
                    hasMore: !(data.body.length === 0),
                });
                window.dispatchEvent(new Event('resize'));
            })
        }
    }

    // handleInfiniteOnLoad = (page) => {
    //     console.log("load more called");
        
    //     this.setState({
    //         loading: true,
    //     });
        
    //     this.fetchProducts(page);
    // };

    onLoadMore = () => {
        this.setState({
          loading: true,
          page: this.state.page + 1
        });
    }

    modelLoaded = (event) => {
        let modelViewers = document.getElementsByClassName("model-viewer");
        let loaded = false;
        for(const modelViewer of modelViewers) {
            if (modelViewer.loaded) {
                loaded = true
            } else {
                loaded = false
            }
        }

        if (loaded) {
            this.setState({
                modelLoading: false
            })
        }
    }

    onViewInAR(item) {
        this.props.updateARViewProduct(item);
    }

    onBuyNow(item) {
        if (this.props.ga4.initialized) {
            this.props.ga4.ga4.gtag('event', 'Buy now', {'name': item.title, 'product_url': item.buy_url});
        }
    }

    renderItem(item) {
        return(
            <List.Item key={item.id}
                actions={[]}
                extra={<></>}>
                    <List.Item.Meta 
                        avatar={<></>}
                        description={""}/>
                        
                        <WingBlank>
                            <div className="shop-item">
                                <div className="shop-item-preview">
                                <ARView src={item.ar_url} poster={item.product_image} ios_src={item.ios_ar_src} id={item.id} title={item.title} modelLoaded={this.modelLoaded}/>
                                </div>
                                <div className="shop-item-detail">
                                    {item.title && <div className="shop-item-title">{item.title.toUpperCase()}</div>}
                                    {item.edition && <div className="shop-item-version">{item.edition.toUpperCase()}</div>}
                                    {item.status && <div className="shop-item-status">{item.status.toUpperCase()}</div>}
                                    {item.price && <div className="shop-item-prie">${item.price}</div>}
                                    {item.description && <div className="shop-item-desc">{item.description}</div>}
                                    <Button className="shop-item-buynow" size="small" type="primary" href={item.buy_url} target="_blank" onClick={this.onBuyNow.bind(this, item)}> Buy Now </Button>
                                </div>
                            </div>
                        </WingBlank>
            </List.Item>
        );
    }

    render() {
        const {products, hasMore, loading, modelLoading, notified} = this.state;
        const loadMore =
            !loading && hasMore ? (
                <div className="shoplist-loadmore">
                    <Button className="shoplist-loadmore-button" onClick={this.onLoadMore} type="primary" style={{color: 'black', fontFamily: 'BebasNeue-bold'}} disabled={modelLoading}>
                    {modelLoading ? "Loading 3D..." : "SHOW MORE"}
                    </Button>
                </div>
            ) : null;
        return(
            <>
            {this.props.player.isSet && !notified && <div className="shoplist-notify">Products related to {this.props.player.name} has been loaded successfully!</div>}
            <div className="shoplist-infinite-container">
                {/* <InfiniteScroll
                    initialLoad={false}
                    pageStart={0}
                    loadMore={this.handleInfiniteOnLoad}
                    hasMore={!this.state.loading && this.state.hasMore}
                    useWindow={true}
                    > */}
                        <List
                            itemLayout="vertical"
                            size="large"
                            dataSource={products}
                            footer={<></>}
                            renderItem={item => this.renderItem(item)}
                            loadMore={loadMore}>
                            {this.state.loading && this.state.hasMore && (
                                <div className="shop-loading-container">
                                    <Spin />
                                </div>
                            )}
                        </List>
                {/* </InfiniteScroll> */}
            </div>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    const {ga4, player} = state;
    return ({
        ga4,
        player
    })
}
const mapDispatchToProps = (dispatch) => {
    return {
        updateARViewProduct: (product) => dispatch(updateARViewProduct(product)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShopList);