import React, {useEffect, useState} from 'react'
import moment from 'moment';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {updateScore, resetScore} from '../../redux/actions/updateScore';
import './scoreview.scss';
import { getGameDetail } from '../../redux/actions/getGameDetail';
import { updateTimer } from '../../redux/actions/updateTimer';
import liveIcon from '../../assets/ic_live.png';

function ScoreView (props){

    const {score, timer, teamSchedule, gameDetail} = props;
    const [showGames, setShowGames] = useState(false);

    useEffect(() => {
      if(gameDetail.gameDetail) {
        props.updateScore(gameDetail.gameDetail)
        props.updateTimer("00:00");
      }
    }, [gameDetail.isSuccess]);

    function openGamesList() {
        setShowGames(!showGames);
    }

    function closeGameList(game, e) {
      setShowGames(false);
      props.getGameDetail(game.gid);
      console.log(game.gid);
    }

    function compareGames(a, b) {
      if ( a.gid < b.gid ){
        return 1;
      }
      if ( a.gid > b.gid ){
        return -1;
      }
      return 0;
    }

    function renderGamesList() {
      const {g: games} = teamSchedule.teamSchedule.gscd;
      games.sort(compareGames);
      var _items = games.map((game) => {

        if (game.gdtutc === 'TBD' || game.utctm === 'TBD') {
          return null;
        }

        var toDay = moment.utc().toDate();
        var gameDate = moment.utc(game.gdtutc + " " + game.utctm).toDate(); 
        if (toDay >= gameDate) {
          let isLive = false;
          if (gameDetail.gameDetail.g.gid === game.gid && gameDetail.gameDetail.g.st === 1) {
            return null;
          }

          if (game.st !== '3' ) {
            isLive = true;
          }
          let time = moment.utc(game.gdtutc + " " + game.utctm).local().format('MM/DD/YYYY @h:mm A');
          return <li className="rivals-game-list-item" key={game.gid} value={game.gid} onClick={closeGameList.bind(this, game)}>
            {game.v.tn + " vs " + game.h.tn + " "+ time} 
            {isLive && <img className="score_live_icon" src={liveIcon} alt="live_icon"/>}
          </li>
        } else {
          return null;
        }
      })
      return (
        <ul className="rivals-game-list">
          {_items}
        </ul>
      );
    }

    function renderContent() {
        const {
            p, hls: leftTeam, vls: rightTeam
          } = score.gameDetail.g;
          const {gdtutc, utctm} = gameDetail.gameDetail.g
        let time = moment.utc(gdtutc + " " + utctm).local().format('MM/DD/YYYY @h:mm A z');
        let playTime = timer.time;
        return (
            <>
            <div className="rivals-timer" onClick={openGamesList}>{leftTeam.tn + " VS " + rightTeam.tn + " " + time}</div>
            {showGames && <div className="rival-game-list-container">{renderGamesList()}</div>}
            <div className="rivals-wrap">
              <div className="rival">
                <div className="rival-team-or-quarter">{rightTeam.tn.toUpperCase()}</div>
                <div className="rival-team-or-quarter-value">{rightTeam.s}</div>
              </div>
              <div className="rival">
                <div className="rival-team-or-quarter quarter">Q{p}</div>
                <div className="rival-team-or-quarter-value quarter">{playTime}</div>
              </div>
              <div className="rival">
                <div className="rival-team-or-quarter right-team">{leftTeam.tn.toUpperCase()}</div>
                <div className="rival-team-or-quarter-value right-team">{leftTeam.s}</div>
              </div>
            </div>
            </>
          );
    }

    function render(){
        
        return (
            <>
            {score.isUpdate && renderContent()}
            </>
        )
    }

    return(
        render()
    )
}

ScoreView.propTypes = {
    score : PropTypes.object.isRequired
}

const mapStateToProps = (state) => {
    const {score, timer, teamSchedule, gameDetail} = state;
    return {
        score: score,
        timer : timer,
        teamSchedule: teamSchedule,
        gameDetail
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateScore : (event) => dispatch(updateScore(event)),
        resetScore : () => dispatch(resetScore()),
        getGameDetail: (gid) => dispatch(getGameDetail(gid)),
        updateTimer: (time) => dispatch(updateTimer(time))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ScoreView);