import React, {Component} from 'react'
import ballIcon from '../../assets/phoenix_suns_ball.png'
import opPlayerImage from '../../assets/ic_circle_black.png';
import { Sprite, Text} from '@inlet/react-pixi';
import {TextStyle} from 'pixi.js';

export default class PlayerIconView extends Component{

    constructor(props) {
        super(props);
        this.state = {
            x : props.x,
            y : props.y,
            nextX : props.x,
            nextY : props.y,
            number : props.number,
            image : props.image,
            player : props.player,
        }
    }

    componentDidMount(){
        this.props.app.ticker.add(this.tick);
    }

    componentWillUnmount(){
        this.props.app.ticker.remove(this.tick);
    }

    move(newX, newY, number, image) {
        this.setState({
            nextX: newX,
            nextY: newY,
            number: number,
            image : image
        });
    }

    updateLocation(newX, newY) {
        this.setState({
            // x: newX,
            // y: newY,
            nextX: newX,
            nextY: newY
        });
    }

    updateNumber(number) {
        this.setState({
            number: number,
        });
    }

// for test
    tick = delta => {
        const {x, y, nextX, nextY} = this.state;

        let dx = nextX - x;
        let dy = nextY - y;

        if (((nextX - dx * 0.1) < x) && (x < nextX) && ((nextY - dy * 0.1) < y) && (y < nextY)) {
            this.setState({
                x : nextX,
                y : nextY
            })
        } else {
            this.setState(({ x , y, nextX, nextY}) => ({
                x : x + dx * 0.1,
                y : y + dy * 0.1,
              }))
        }
    }


    render(){
        const{x, y, number, image} = this.state;
        var width = 30;
        var height = 30;
        if (image === ballIcon) {
            width = 20; height=20;
        }
        var textColor = ['#000000', '#000000'];
        if (image === opPlayerImage) {
            textColor = ['#FFFFFF', '#FFFFFF']
        }

        const style = new TextStyle({
            fontWeight: 'bold',
            fill: textColor, // gradient
            fontFamily: 'BebasNeue',
            fontSize: 20,
          });

        return(
        <>
            <Sprite
                anchor={0.5}
                x={x}
                y={y}
                width={width}
                height={height}
                image={image}>
            </Sprite>
            <Text 
                anchor={0.5}
                text={number} 
                x={x}
                y={y}
                width={8}
                height={12}
                align="center"
                style={style}>
            </Text>
        </>
        )
    };
}