import actionConstants from '../constants/ActionConstants';
import actionDispatcher from './ActionDispatcher';
import isShotTracker from '../../api/isShotTracker';

const getShotTracker = () => (
    actionDispatcher(isShotTracker.bind(null),
      actionConstants.GET_ST_SUCCESS,
      actionConstants.GET_ST_FAILURE,
      actionConstants.GET_ST_REQUEST)
  );
  
  const resetShotTracker = () => (dispatch) => {
    dispatch({ type: actionConstants.GET_ST_RESET });
  };
  
  export {
    getShotTracker,
    resetShotTracker,
  };
  