import actionConstants from '../constants/ActionConstants';

const initialState = {
    isPlaying: false,
    time : 0
  };

  export default function timer(state = initialState, action) {
    switch (action.type) {
        case actionConstants.START_TIMER:
            return {
            ...state,
            isPlaying: true,
            };
        case actionConstants.STOP_TIMER:
            return{
                ...state,
                isPlaying : false,
            }
        case actionConstants.UPDATE_TIMER:
            return{
                ...state,
                time: action.time,
            }
        default:
          return state;
      }
    
}
