import actionConstants from '../constants/ActionConstants';

export function updateOPlayersList(players, teamId) {
    return { 
        type: actionConstants.UPDATE_O_PLAYERS_LIST,
        players: players,
        teamId: teamId
    }; 
}

export function resetOPlayersList() {
    return {
        type: actionConstants.RESET_O_PLAYERS_LIST,
    }
}