import call from './apiRequest';
import apiEndpoints from '../common/constants/apiEndpoints';
/**
 * Api call for fetching game details
 * @param {object} data
 */
const fetchGameDetailPbp = (gameId, quater) => call({
  endpoint: `${apiEndpoints.gameDetailPbp}/${gameId}/${quater}`,
  baseUrlType: 'serverUrl',
});

export default fetchGameDetailPbp;