import React from 'react';
import { Link } from 'react-router-dom';
import routes from '../../common/constants/routes';
import './header.scss';

function Header() {
  const { swagShop, social, default: onCourt } = routes;

  return (
    <div className="header-tab-wrap">
      <div className="header-tab">
        <Link to={onCourt} className={`tab-wrap ${window.location.pathname === onCourt ? 'active-tab' : ''}`}>
          <div className="tab">ON COURT</div>
        </Link>
        <Link to={swagShop} className={`tab-wrap middle-tab ${window.location.pathname === swagShop ? 'active-tab' : ''}`}>
          <div className="tab">SWAG SHOP</div>
        </Link>
        <Link to={social} className={`tab-wrap last-tab ${window.location.pathname === social ? 'active-tab' : ''}`}>
          <div className="tab">SOCIAL</div>
        </Link>
      </div>
    </div>
  );
}

export default Header;
