import call from './apiRequest';
import apiEndpoints from '../common/constants/apiEndpoints';

/**
 * Api call for fetching game details
 * @param {object} data
 */
const fetchShopProducts = () => call({
  endpoint: `${apiEndpoints.shopProducts}`,
  baseUrlType: 'serverUrl',
});

const fetchShopProductsLimit = (page, limit) => call({
  endpoint: `${apiEndpoints.shopProducts}/${page}/${limit}`,
  baseUrlType: 'serverUrl',
});

const fetchPlayerProductsLimit = (pid, page, limit) => call({
  endpoint: `${apiEndpoints.playerproducts}/${pid}/${page}/${limit}`
})



export {fetchShopProducts, fetchShopProductsLimit, fetchPlayerProductsLimit};