import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Card } from 'antd';
import 'antd/dist/antd.css';
import './socialfeedlist.scss';
import { Timeline } from 'react-twitter-widgets';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { isMobile } from 'react-device-detect';

class SocialFeedList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            dimensions : null,
            players: props.players
        }

        this.handleResize = this.handleResize.bind(this);
    }

    componentDidMount() {
        this.setState({
            dimensions: {
                width: this.container.offsetWidth,
                height: this.container.offsetHeight,
            },
        });
        window.addEventListener('resize', this.handleResize);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    handleResize(){
        this.setState({dimensions : {width: this.container.offsetWidth, height: this.container.offsetHeight}});
    }

    renderTwitterCarousel(players) {
        return (
            <div className="social-item-container">
                <Carousel autoPlay={false} autoplaySpeed={3500} showArrows={!isMobile} showThumbs={false} showStatus={false} infiniteLoop={true}>
                    {
                        players.map(player => {
                            return (
                                <div className="social-item-carousel-item" key={player.id}>
                                    <Card title={"@" + player.twitter_name} bordered={false} style={{background: "#17202a", height:"520px"}} headStyle={{color:"#FFF", fontSize:"1.2rem"}}>
                                        {player.twitter_name && <Timeline  dataSource={{ sourceType: 'profile', screenName: player.twitter_name}} options={{ height: '400', theme: 'dark'}}/>}
                                    </Card>
                                </div>
                            );
                        })
                    }
                </Carousel>
            </div>
        );
    }

    renderSpotifyCarousel(players) {
        const {dimensions} = this.state;
        return (
            dimensions && <div className="social-item-container">
                <Carousel autoplaySpeed={5000} showArrows={!isMobile} showThumbs={false} showStatus={false} infiniteLoop={true} autoPlay={false}>
                    {
                        players.map(player => {
                            return (
                                <div className="social-item-carousel-item" key={player.id}>
                                    <Card title={"@" + player.twitter_name} bordered={false} style={{background: "#121212", height:"520px"}} headStyle={{color:"#FFF", fontSize:"1.2rem"}}>
                                    {player.spotify_name && <iframe title={player.id} src={"https://open.spotify.com/embed/playlist/" + player.spotify_name} width={dimensions.width - 100} height="380" allowtransparency="true" allow="encrypted-media"></iframe>}
                                    </Card>
                                </div>
                            );
                        })
                    }
                </Carousel>
            </div>
        );
    }

    render() {
        const {players} = this.state;
        if (this.props.player.isSet) {
            let selectedPlayer = players.filter(val => val.player_id === `${this.props.player.pid}`);
            return(
                <div className="social-infinite-container" ref={el => (this.container = el)}>
                    {this.renderTwitterCarousel(selectedPlayer)}
                    {this.renderSpotifyCarousel(selectedPlayer)}
                </div>
            );
        } else {
            return(
                <div className="social-infinite-container" ref={el => (this.container = el)}>
                    {this.renderTwitterCarousel(players)}
                    {this.renderSpotifyCarousel(players)}
                </div>
            );
        }
    }
}

const mapStateToProps = (state) => {
    const { player } = state;
    return ({
        player
    })
}

export default connect(mapStateToProps)(SocialFeedList);