import actionConstants from '../constants/ActionConstants';

const initialState = {
    pid: null,
    name: null,
    isSet: false,
  };
  
export default function player(state = initialState, action) {
    switch (action.type) {
      case actionConstants.SET_PLAYER:
        return {
          ...state,
          pid : action.pid,
          name: action.name,
          isSet: true
        };
        case actionConstants.RESET_PLAYER:
          return{
              ...state,
              isSet : false
          }
      default:
        return state;
    }
}