import React, { createElement, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import config from '../../config';
import './player.scss';
import placeHolder from '../../assets/male-placeholder.png';
import { connect } from 'react-redux';
import { Popover } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { setPlayer } from '../../redux/actions/updatePlayer';
import { isMobile } from 'react-device-detect';

const { API } = config;

function Player(props) {
  const { details, index} = props;
  const {
    fn,
    ln,
    num,
    pos,
    reb,
    ast,
    pts,
    pid,
  } = details;
  const history = useHistory();
  const [popview, setPopview] = useState(false);
  function getPlaceHolder() {
      return placeHolder;
  }

  function onClickImage() {
    if (popview) {
      setPopview(false)
    } else {
      setPopview(true)
    }
  }

  const onSelect = (opt) => {
    setPopview(false)
    removeCloseView()
    if (opt.props.value === 'shop') {
      props.setPlayer(pid, `${fn} ${ln}`)
      history.push('swagShop')
    } else if (opt.props.value === 'social') {
      props.setPlayer(pid, `${fn} ${ln}`)
      history.push('social');
    } else {
      console.log("cancel");
    }
  };

  const handleVisibleChange = (visible) => {
    setPopview(visible)
    if (!isMobile) {
      if (visible) {
        appendCloseView()
      }
    }
  };

  const hidePopview = (src) => {
    setPopview(false)
    src.srcElement.parentNode.removeChild(src.srcElement)
  }

  const appendCloseView = () => {
    const closeview = document.getElementsByClassName('player-popview-remove');
    if (closeview.length <= 0) {
      const close = document.createElement('div');
      close.className = 'player-popview-remove'
      close.addEventListener('click', hidePopview)
      document.getElementsByClassName('app-wrap')[0].appendChild(close);
    }
  }

  const removeCloseView = () => {
    const closeview = document.getElementsByClassName('player-popview-remove');
    if (closeview.length > 0) {
      closeview[0].parentNode.removeChild(closeview[0])
    }
  }

  function renderPopview() {
    let align = {
      overflow: { adjustY: 0, adjustX: 0 },
      offset: [-10, 0],
    }
    let placement = 'bottom';
    if (isMobile) {
      if (index === 0) {
        align = {
          overflow: { adjustY: 0, adjustX: 0 },
          offset: [12, 0],
        }
        placement = 'bottomLeft';
      } else if (index === 4) {
        align = {
          overflow: { adjustY: 0, adjustX: 0 },
          offset: [-19, 0],
        }
        placement = 'bottomRight';
      }
    }
    return (
      <Popover mask 
            overlayClassName="player-view"
            overlayStyle={{ color: 'currentColor' }}
            visible={popview}
            overlay={[
              (<Popover.Item key={`${pid}-0`} value="shop" style={{cursor: 'pointer'}} >View Products</Popover.Item>),
              (<Popover.Item key={`${pid}-1`} value="social" style={{cursor: 'pointer'}}>View Lifestyle</Popover.Item>),
            ]}
            align={align}
            onVisibleChange={handleVisibleChange}
            onSelect={onSelect}
            placement={placement}
          >
            <div style={{
              padding: '0 15px',
              marginRight: '-15px',
              display: 'flex',
              alignItems: 'center',
            }}
            >
            </div>
          </Popover>
    )
  }

  return (
    <>
      <div className="players-details-wrap">
        <div className="players-image" onClick={onClickImage}>
          <img src={`${API.baseImagesUrl}${pid}.png`} alt={`${fn} ${ln}`} onError={getPlaceHolder}/>
        </div>
        {renderPopview()}
        <div className="players-details">
          <div className="players-score-head">
            <div className="score">{num}</div>
            <div className="player-position">{pos}</div>
          </div>
          <div className="player-name">{`${fn.toUpperCase()}`}</div>
          <div className="player-name">{`${ln.toUpperCase()}`}</div>
          <div className="players-score-wrap">
            <ul className="players-score-ul">
              <li className="score-li">
                <span>PTS</span>
                <span>{pts}</span>
              </li>
              <li className="score-li">
                <span>REB</span>
                <span>{reb}</span>
              </li>
              <li className="score-li">
                <span>AST</span>
                <span>{ast}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
      {/* {popview && appendCloseView()} */}
    </>
  );
}

Player.propTypes = {
  details: PropTypes.object.isRequired,
  teamId: PropTypes.number.isRequired,
};

const mapDispatchToProps = (dispatch) => {
  return {
    setPlayer: (pid, name) => dispatch(setPlayer(pid, name))
  }
}

export default connect(null, mapDispatchToProps)(Player);
