import React,{useEffect, useState} from 'react';
import { connect } from 'react-redux';
import ShopCarousel from '../../components/ShopCarousel';
import ShopList from '../../components/ShopList';
import fetchSeasonPlayers from '../../api/fetchSeasonPlayers';
import { getTeamSchedule, resetTeamSchedule } from '../../redux/actions/getTeamSchedule';
import { getGameDetail, resetGameDetail } from '../../redux/actions/getGameDetail';
import {resetARViewProduct} from '../../redux/actions/updateARViewProduct';
import {ActivityIndicator} from 'antd-mobile';
import {resetPlayer} from '../../redux/actions/updatePlayer';
import PropTypes from 'prop-types';
import './shop.scss';

function Shop(props) {
    const [load, setLoad] = useState(false);
    const [players, setPlayers] = useState(null);
    const [visitTime] = useState(new Date());
    useEffect(() => {
        if(!load) {
            setLoad(true);
            fetchPlayers();    
        }
    }, [load]);

    useEffect(() => {
        if(props.ga4.initialized) {
            props.ga4.ga4.pageview('SwagShop');
            props.ga4.ga4.gtag('event', 'visit_page', {'page': 'SwagShop'});
        }
        setupBeforeUnloadListener();
        return () => {
            if (props.ga4.initialized) {
                let current = new Date();
                let duration = current - visitTime
                props.ga4.ga4.gtag('event', 'leave_page', {'page': 'SwagShop', 'duration': duration});
                removeBeforeUnloadListener();
            }
            
            if (props.player.isSet) {
                props.resetPlayer();
            }
        }
    }, []);

    function reportBeforeUnload() {
        if (props.ga4.initialized) {
            let current = new Date();
            let duration = current - visitTime
            props.ga4.ga4.gtag('event', 'leave_page', {'page': 'SwagShop', 'duration': duration});
        }
    }
    
    function setupBeforeUnloadListener() {
        window.addEventListener("beforeunload", onBeforeUnload);
    };

    function removeBeforeUnloadListener() {
        window.removeEventListener("beforeunload", onBeforeUnload);
    }

    function onBeforeUnload(ev) {
        ev.preventDefault();
        return reportBeforeUnload();
    }

    function fetchPlayers() {
        fetchSeasonPlayers('2020').then((data) => {
            console.log(data.body);
            setPlayers(data.body);
        });
    }

    return(
        <> 
        {
            players &&
            <div className="container">
                {<ShopCarousel players={players}/>}
                {<ShopList/>}
            </div>
        }
        {
            (!players) && (
                <div className="indicator-container">
                    <ActivityIndicator color="blue" size="large" text=""/>
                </div>
            )
        }
        </>
    );
}

const mapStateToProps = (state) => {
    const { arviewProduct, ga4, player} = state;
    return ({
        arviewProduct,
        ga4,
        player
    });
  };
  
  Shop.propTypes = {
    getTeamSchedule: PropTypes.func.isRequired,
    resetTeamSchedule: PropTypes.func.isRequired,
    getGameDetail: PropTypes.func.isRequired,
    resetGameDetail: PropTypes.func.isRequired,
  };

  const mapDispatchToProps = (dispatch) => {
      return {
        getTeamSchedule,
        resetTeamSchedule,
        getGameDetail,
        resetGameDetail,
        resetARProduct: () => dispatch(resetARViewProduct()),
        resetPlayer: () => dispatch(resetPlayer())
      }
  }

export default connect(mapStateToProps, mapDispatchToProps)(Shop);