import actionConstants from '../constants/ActionConstants';

const initialState = {
  error: '',
  gameDetail: null,
  isFetching: false,
  isSuccess: false,
};

export default function gameDetail(state = initialState, action) {
  switch (action.type) {
    case actionConstants.GET_GAME_DETAIL_REQUEST:
      return {
        ...state,
        error: '',
        isFetching: true,
        isSuccess: false,
      };
    case actionConstants.GET_GAME_DETAIL_SUCCESS:
      return {
        ...state,
        gameDetail: action.payload,
        isFetching: false,
        isSuccess: true,
      };
    case actionConstants.GET_GAME_DETAIL_FAILURE:
      return {
        ...state,
        error: action.message,
        isFetching: false,
      };
    case actionConstants.GET_GAME_DETAIL_RESET:
      return {
        ...state,
        error: '',
        isSuccess: false,
        isFetching: false,
      };
    case actionConstants.RESET:
      return {
        ...initialState,
      };
    case actionConstants.SET_GAME_DETAIL:
      return{
        ...state,
        gameDetail: action.gameDetail,
      };
    default:
      return state;
  }
}
