import React, { Component, createRef} from 'react';
import {connect} from 'react-redux';
import courtImage from '../../assets/court_2.jpg';
import playerImage from '../../assets/ic_player.png';
import opPlayerImage from '../../assets/ic_circle_black.png';
import './courtview.scss';
import {Container, Stage, Sprite, AppConsumer } from '@inlet/react-pixi';
import PlayerIconView from '../PlayerIconView/index';
import ballIcon from '../../assets/phoenix_suns_ball.png';
import { sortPlayers } from '../../common/helpers/commonUtils';
const positions = ['C', 'PG', 'SG', 'PF', 'SF'];

class CourtView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            dimensions : null,
            lastEvent : props.lastEvent,

            locpg : [0, 0],
            locc : [0, 0],
            locsg : [0, 0],
            locpf : [0, 0],
            locsf : [0, 0],

            locball : [0, 0],

            olocpg : [0, 0],
            olocc : [0, 0],
            olocsg : [0, 0],
            olocpf : [0, 0],
            olocsf : [0, 0],
        }
        
        this.pgRef = createRef();
        this.cRef = createRef();
        this.sgRef = createRef();
        this.pfRef = createRef();
        this.sfRef = createRef();

        this.ballRef = createRef();
        this.handleResize = this.handleResize.bind(this);

        this.opgRef = createRef();
        this.ocRef = createRef();
        this.osgRef = createRef();
        this.opfRef = createRef();
        this.osfRef = createRef();
    }

    componentDidMount() {
        this.setState({
            dimensions: {
                width: this.container.offsetWidth,
                height: this.container.offsetHeight,
            },
        });
        window.addEventListener('resize', this.handleResize);
    }

    componentDidUpdate(prevProps, prevState) {
        if(this.props.event.eventDetail && this.props.gameDetail.gameDetail) {
            this.updateEvent(this.props.players.players, this.props.oPlayers.players, this.props.event.eventDetail);
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    handleResize(){
        this.setState({dimensions : {width: this.container.offsetWidth, height: this.container.offsetHeight}});
    }

    getSunsTeam(game) {

        const leftTeam = game.g.hls;
        const rightTeam = game.g.vls;
        let _sunsTeamDetails = leftTeam;
        if (rightTeam.tn === 'Suns') {
            _sunsTeamDetails = rightTeam;
        }
        return _sunsTeamDetails;
    }

    getOpTeam(game) {
        const leftTeam = game.g.hls;
        const rightTeam = game.g.vls;
        let _opTeam = rightTeam;
        if (rightTeam.tn === 'Suns') {
            _opTeam = leftTeam;
        }
        return _opTeam;
    }

    updateEvent(newPlayers, newOpPlayers, lastEvent) {
        
        const sunsTeamDetails = this.getSunsTeam(this.props.gameDetail.gameDetail);
        if (sunsTeamDetails.tid === lastEvent.tid) {

            newPlayers.forEach(player => {
                if (player.pos === "PG" && this.pgRef.current) {
                    if(player.pid === lastEvent.pid) {
                        var pg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.pgRef.current.move(pg[0], pg[1], player.num, playerImage);      
                    }
                    this.pgRef.current.updateNumber(player.num);
                } else if (player.pos === "C" && this.cRef.current) {
                    if(player.pid === lastEvent.pid) {
                        var c = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.cRef.current.move(c[0], c[1], player.num, playerImage); 
                    }
                    this.cRef.current.updateNumber(player.num);
                } else if (player.pos === "SG" && this.sgRef.current) {
                    if(player.pid === lastEvent.pid) {
                        var sg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.sgRef.current.move(sg[0], sg[1], player.num, playerImage); 
                    }
                    this.sgRef.current.updateNumber(player.num);
                } else if (player.pos === "PF" && this.pfRef.current) {
                    if(player.pid === lastEvent.pid) {
                        var pf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.pfRef.current.move(pf[0], pf[1], player.num, playerImage); 
                    }
                    this.pfRef.current.updateNumber(player.num);
                } else if (player.pos === "SF" && this.sfRef.current) {
                    if(player.pid === lastEvent.pid) {
                        var sf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.sfRef.current.move(sf[0], sf[1], player.num, playerImage); 
                    }
                    this.sfRef.current.updateNumber(player.num);
                }
            });  
        } else {
            
            newOpPlayers.forEach(player => {
                if (player.pos === "PG" && this.opgRef.current) {
                    if (player.pid === lastEvent.pid) {
                        var pg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.opgRef.current.move(pg[0], pg[1], player.num, opPlayerImage); 
                    }
                    this.opgRef.current.updateNumber(player.num);
                } else if (player.pos === "C" && this.ocRef.current) {
                    if (player.pid === lastEvent.pid) {
                        var c = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.ocRef.current.move(c[0], c[1], player.num, opPlayerImage); 
                    }
                    this.ocRef.current.updateNumber(player.num);
                } else if (player.pos === "SG" && this.osfRef.current) {
                    if (player.pid === lastEvent.pid) {
                        var sg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.osgRef.current.move(sg[0], sg[1], player.num, opPlayerImage); 
                    }
                    this.osgRef.current.updateNumber(player.num);
                } else if (player.pos === "PF" && this.opfRef.current) {
                    if (player.pid === lastEvent.pid) {
                        var pf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.opfRef.current.move(pf[0], pf[1], player.num, opPlayerImage); 
                    }
                    this.opfRef.current.updateNumber(player.num);
                } else if (player.pos === "SF" && this.osfRef.current) {
                    if (player.pid === lastEvent.pid) {
                        var sf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                        this.osfRef.current.move(sf[0], sf[1], player.num, opPlayerImage); 
                    }
                    this.osfRef.current.updateNumber(player.num);
                }
            });
        }
        
        var ball = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
        this.ballRef.current.move(ball[0] + 6, ball[1] + 10, "", ballIcon);
    }

    convertLocToPx(locX, locY, teamId) {
        const {dimensions} = this.state;
        //Court unit size x : [-250, 250], y : [-80, 840] -> (-420, 420) -> (-500, 500)
        var y_rate = (dimensions.width / 4) / (250 + 40); // out side border blue area
        var x_rate = (dimensions.width / 2) / (500 + 50); // out side border blue area
        var x = x_rate * locY; // for origin zero point for locY ()
        var y = y_rate * locX;

        const {
            vls: rightTeam
        } = this.props.gameDetail.gameDetail.g;
        if (teamId === rightTeam.tid) {
            x = -(420 - locY) * x_rate;
            y = y_rate * locX;
        } else {
            x = (420 - locY) * x_rate;
            y = -y;
        }
        return [x, y]
    }

    convertPercentPointToPix(xp, yp) {
        const { dimensions } = this.state;
        var length_rate = (dimensions.width / 2) / 100;
        var width_rate = length_rate / 2;
        var x = length_rate * xp;
        var y = width_rate * yp;
        return [x, y];
    }
    
    renderContent() {
        const { dimensions, locpg, locc, locsg, locpf, locsf, olocpg, olocc, olocsg, olocpf, olocsf} = this.state;
        const { gameDetail, oPlayers } = this.props; //1-pg 2-c 3-sg 4-pf 5-sf
        if (!gameDetail || !gameDetail.gameDetail) return;
        const {
            lpla: levent, hls: leftTeam
        } = gameDetail.gameDetail.g;
        const sunsTeamDetails = this.getSunsTeam(gameDetail.gameDetail);
        const opPlayers = oPlayers.players;
        const players = this.props.players.players;
        if (!players || !opPlayers) {
            return null;
        }
        sortPlayers(players).map((player) => {
            if (positions.includes(player.pos)) {
                return player;
            }
            return null;
        });
        sortPlayers(opPlayers).map((player) => {
            if (positions.includes(player.pos)) {
                return player;
            }
            return null;
        });
        var pg = locpg; 
        var c =  locc;
        var sg =locsg;
        var pf = locpf;
        var sf = locsf;

        var opg = olocpg; 
        var oc =  olocc;
        var osg =olocsg;
        var opf = olocpf;
        var osf = olocsf;

        var lastEvent = levent;
        if (this.props.event.eventDetail) {
            lastEvent = this.props.event.eventDetail;
        }
        var locBall = this.convertLocToPx(lastEvent.locX, lastEvent.locY);
        if (leftTeam.tid === sunsTeamDetails.tid) {
            if (locpg[0] === 0 && locpg[1] === 0) {pg=this.convertPercentPointToPix(30, 0)}
            if (locc[0] === 0 && locc[1] === 0) {c=this.convertPercentPointToPix(66, 33)}
            if (locsg[0] === 0 && locsg[1] === 0) {sg=this.convertPercentPointToPix(35, -45)}
            if (locpf[0] === 0 && locpf[1] === 0) {pf=this.convertPercentPointToPix(75, -40)}
            if (locsf[0] === 0 && locsf[1] === 0) {sf=this.convertPercentPointToPix(80, 50)}

            if (olocpg[0] === 0 && olocpg[1] === 0) {opg=this.convertPercentPointToPix(-30, 0)}
            if (olocc[0] === 0 && olocc[1] === 0) {oc=this.convertPercentPointToPix(-66, -33)}
            if (olocsg[0] === 0 && olocsg[1] === 0) {osg=this.convertPercentPointToPix(-35, 45)}
            if (olocpf[0] === 0 && olocpf[1] === 0) {opf=this.convertPercentPointToPix(-75, 40)}
            if (olocsf[0] === 0 && olocsf[1] === 0) {osf=this.convertPercentPointToPix(-80, -50)}
        } else {
            if (locpg[0] === 0 && locpg[1] === 0) {pg=this.convertPercentPointToPix(-30, 0)}
            if (locc[0] === 0 && locc[1] === 0) {c=this.convertPercentPointToPix(-66, -33)}
            if (locsg[0] === 0 && locsg[1] === 0) {sg=this.convertPercentPointToPix(-35, 45)}
            if (locpf[0] === 0 && locpf[1] === 0) {pf=this.convertPercentPointToPix(-75, 40)}
            if (locsf[0] === 0 && locsf[1] === 0) {sf=this.convertPercentPointToPix(-80, -50)}

            if (olocpg[0] === 0 && olocpg[1] === 0) {opg=this.convertPercentPointToPix(30, 0)}
            if (olocc[0] === 0 && olocc[1] === 0) {oc=this.convertPercentPointToPix(66, 33)}
            if (olocsg[0] === 0 && olocsg[1] === 0) {osg=this.convertPercentPointToPix(35, -45)}
            if (olocpf[0] === 0 && olocpf[1] === 0) {opf=this.convertPercentPointToPix(75, -40)}
            if (olocsf[0] === 0 && olocsf[1] === 0) {osf=this.convertPercentPointToPix(80, 50)}
        }

        if (sunsTeamDetails.tid === lastEvent.tid) {
            players.filter((player) => player.pid === lastEvent.pid).map((player) => {
                if (player.pos === "PG") {
                    pg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "C") {
                    c = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "SG") {
                    sg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "PF") {
                    pf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "SF") {
                    sf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                }
                return player;
            });
        } else {
            opPlayers.filter((player) => player.pid === lastEvent.pid).map((player) => {
                if (player.pos === "PG") {
                    opg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "C") {
                    oc = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "SG") {
                    osg = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "PF") {
                    opf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                } else if (player.pos === "SF") {
                    osf = this.convertLocToPx(lastEvent.locX, lastEvent.locY, lastEvent.tid);
                }
                return player;
            });
        }
        
        var width = dimensions.width;
        var height = dimensions.width / 2;

        return (
            <>
            <div className="playground-container" >
            <Stage width={width} height={height} options={{ backgroundColor: 0xffffff, autoDensity:true, forceCanvas:false, sharedLoader:true }} >
                <Container position={[width / 2, height / 2]} width={width} height={height}>
                    <Sprite  anchor={0} x={-width / 2} y={-height / 2} width={width} height={width / 2} image={courtImage} />
                    {players[0]&&<AppConsumer>
                        {app => <PlayerIconView className="pg" image={playerImage} number={players[0].num} x={pg[0]} y={pg[1]} ref={this.pgRef} app={app}/>}</AppConsumer>}
                    {players[1]&&<AppConsumer>
                        {app => <PlayerIconView className="c" image={playerImage} number={players[1].num} x={c[0]} y={c[1]} ref={this.cRef} app={app}/> }</AppConsumer>}
                    {players[2]&&<AppConsumer>
                        {app => <PlayerIconView className="sg" image={playerImage} number={players[2].num} x={sg[0]} y={sg[1]} ref={this.sgRef} app={app}/>}</AppConsumer>}
                    {players[3]&&<AppConsumer>
                        {app => <PlayerIconView className="pf" image={playerImage} number={players[3].num} x={pf[0]} y={pf[1]} ref={this.pfRef} app={app}/>}</AppConsumer>}
                    {players[4]&&<AppConsumer>
                        {app => <PlayerIconView className="sf" image={playerImage} number={players[4].num} x={sf[0]} y={sf[1]} ref={this.sfRef} app={app}/>}</AppConsumer>}

                    {opPlayers[0]&&<AppConsumer>
                        {app => <PlayerIconView className="opg" image={opPlayerImage} number={opPlayers[0].num} x={opg[0]} y={opg[1]} ref={this.opgRef} app={app}/>}</AppConsumer>}
                    {opPlayers[1]&&<AppConsumer>
                        {app => <PlayerIconView className="oc" image={opPlayerImage} number={opPlayers[1].num} x={oc[0]} y={oc[1]} ref={this.ocRef} app={app}/> }</AppConsumer>}
                    {opPlayers[2]&&<AppConsumer>
                        {app => <PlayerIconView className="osg" image={opPlayerImage} number={opPlayers[2].num} x={osg[0]} y={osg[1]} ref={this.osgRef} app={app}/>}</AppConsumer>}
                    {opPlayers[3]&&<AppConsumer>
                        {app => <PlayerIconView className="opf" image={opPlayerImage} number={opPlayers[3].num} x={opf[0]} y={opf[1]} ref={this.opfRef} app={app}/>}</AppConsumer>}
                    {opPlayers[4]&&<AppConsumer>
                        {app => <PlayerIconView className="osf" image={opPlayerImage} number={opPlayers[4].num} x={osf[0]} y={osf[1]} ref={this.osfRef} app={app}/>}</AppConsumer>}

                    <AppConsumer>
                        {app => <PlayerIconView x={locBall[0] + 5} y={locBall[1] + 5} number={""} image={ballIcon} ref={this.ballRef} app={app}/>}</AppConsumer>
                </Container>
            </Stage>
            </div>
            </>
        );
    }
    
      render() {
        const { dimensions } = this.state;
    
        return (
          <div className="court-webgl-bg" ref={el => (this.container = el)}>
            {dimensions && this.renderContent()}
          </div>
        );
      }
}
const mapStateToProps = (state) => {
    const { gameDetail, event, players, oPlayers} = state;
    return{
        gameDetail: gameDetail,
        players : players,
        oPlayers : oPlayers,
        event : event,
    }
}

export default connect(mapStateToProps, null, null, {forwardRef:true})(CourtView);