import superagent from 'superagent';
import config from '../config';

const { API } = config;
const BASE_URL_FOR_TYPE = {
  default: API.serverUrl,
  image: API.baseImagesUrl,
  serverUrl: API.serverUrl,
  socketUrl: API.socketUrl,
};

const defaultHeaders = {
  'X-Busbud-Token': 'PARTNER_BaASYYHxTxuOINEOMWq5GA',
  Accept: 'application/vnd.busbud+json; version=2; profile=https://schema.busbud.com/v2/',
  'Access-Control-Allow-Origin': true,
};

export default function request({
  method = 'get',
  url,
  endpoint,
  payload,
  query,
  type = 'application/json',
  responseType = '',
  baseUrlType = 'default',
}) {
  const _url = url || `${BASE_URL_FOR_TYPE[baseUrlType]}/${endpoint}`;

  const apiRequest = superagent(method, _url);

  apiRequest.set(defaultHeaders);

  if (responseType === 'blob') {
    apiRequest.responseType('blob');
  }
  return (
    new Promise((resolve, reject) => {
      apiRequest
        .set('Content-Type', type)
        .send(payload)
        .query(query)
        .then(resolve)
        .catch((error) => {
          const errorBody = (error.response && error.response.body) || {};
          reject(errorBody);
        });
    })
  );
}
