import React, { Component} from 'react';
import './arview.scss';
import ar_icon from '../../assets/ar_icon_trans.png';
import { connect } from 'react-redux';

class ARView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            src : props.src,
            poster : props.poster,
            ios_src: props.ios_src,
            id: props.id,
            title: props.title,
        }
        this.viewInAR = this.viewInAR.bind(this);
        this.load3D = this.load3D.bind(this);
        this.addEventListener = this.addEventListener.bind(this);
    }

    componentDidMount() {
        this.addEventListener();
    }

    viewInAR(src, title) {
        if (this.props.ga4.initialized) {
            this.props.ga4.ga4.gtag('event', 'View in AR', {'name': title, 'model_url': src});
        }
    }

    load3D() {
        this.setState({
            src: this.props.src,
            ios_src: this.props.ios_src
        });
    }

    addEventListener() {
        let props = this.props;
        const modelViewer = document.getElementById(`model-viewer-${props.id}`);
        modelViewer.addEventListener('model-visibility', function(evt) {
            props.modelLoaded(evt);
        });
        
        // modelViewer.addEventListener('load', function(evt) {
        // });
    }

    render() {
        const {src,poster, ios_src, id, title} = this.state;
        return (
            <div className="arview-container" onClick={this.load3D}> 
                <model-viewer id={`model-viewer-${id}`} class="model-viewer" loading="eager" interaction-prompt="auto" quick-look-browsers="safari chrome" ar camera-controls auto-rotate 
                ar-modes="webxr scene-viewer quick-look"
                ar-scale="auto" 
                poster={poster} 
                src={src} alt=""
                ios-src={ios_src}
                data-js-focus-visible>
                    <button 
                        slot="ar-button" id="ar-button" onClick={this.viewInAR.bind(this, src, title)}>
                        <img src={ar_icon} alt="AR icon"/>
                        <span className="ar-button-text">View in AR</span>
                    </button>
                </model-viewer>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    const {ga4} = state;
    return ({
        ga4
    })
}

export default connect(mapStateToProps)(ARView);