import actionConstants from '../constants/ActionConstants';
import actionDispatcher from './ActionDispatcher';
import fetchTeamSchedule from '../../api/fetchTeamSchedule';

const getTeamSchedule = () => (
  actionDispatcher(fetchTeamSchedule.bind(null),
    actionConstants.GET_TEAM_SCHEDULE_SUCCESS,
    actionConstants.GET_TEAM_SCHEDULE_FAILURE,
    actionConstants.GET_TEAM_SCHEDULE_REQUEST)
);

const resetTeamSchedule = () => (dispatch) => {
  dispatch({ type: actionConstants.GET_TEAM_SCHEDULE_RESET });
};

export {
  getTeamSchedule,
  resetTeamSchedule,
};
