import actionConstants from '../constants/ActionConstants';
import actionDispatcher from './ActionDispatcher';
import fetchGameDetail from '../../api/fetchGameDetail';

const getGameDetail = (gameId) => (
  actionDispatcher(fetchGameDetail.bind(null, gameId),
    actionConstants.GET_GAME_DETAIL_SUCCESS,
    actionConstants.GET_GAME_DETAIL_FAILURE,
    actionConstants.GET_GAME_DETAIL_REQUEST)
);

const resetGameDetail = () => (dispatch) => {
  dispatch({ type: actionConstants.GET_GAME_DETAIL_RESET });
};

const setGameDetail = (gameDetail) => {
  return {
    type: actionConstants.SET_GAME_DETAIL,
    gameDetail : gameDetail,
  }
}

export {
  getGameDetail,
  resetGameDetail,
  setGameDetail,
};
