import actionConstants from '../constants/ActionConstants';

const initialState = {
    eventDetail: null,
    isUpdate: false,
  };
  
export default function event(state = initialState, action) {
    switch (action.type) {
      case actionConstants.UPDATE_GAME_EVENT:
        return {
          ...state,
          eventDetail : action.event,
          isUpdate: true
        };
      case actionConstants.RESET_GAME_EVENT:
        return{
            ...state,
            isUpdate : false
        }
      default:
        return state;
    }
}