import React, { Component, createRef} from 'react';
import {connect} from 'react-redux';
import courtImage from '../../assets/court_2.jpg';
import playerImage from '../../assets/ic_player.png';
import opPlayerImage from '../../assets/ic_circle_black.png';
import './courtview.scss';
import {Container, Stage, Sprite, AppConsumer } from '@inlet/react-pixi';
import PlayerIconView from '../PlayerIconView/index';
import ballIcon from '../../assets/phoenix_suns_ball.png';

class STCourtView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            dimensions : null,
            lastEvent : props.lastEvent,

            locpg : [0, 0],
            locc : [0, 0],
            locsg : [0, 0],
            locpf : [0, 0],
            locsf : [0, 0],

            locball : [0, 0],

            olocpg : [0, 0],
            olocc : [0, 0],
            olocsg : [0, 0],
            olocpf : [0, 0],
            olocsf : [0, 0],
        }
        
        this.pgRef = createRef();
        this.cRef = createRef();
        this.sgRef = createRef();
        this.pfRef = createRef();
        this.sfRef = createRef();

        this.ballRef = createRef();
        this.handleResize = this.handleResize.bind(this);

        this.opgRef = createRef();
        this.ocRef = createRef();
        this.osgRef = createRef();
        this.opfRef = createRef();
        this.osfRef = createRef();
    }

    componentDidMount() {
        this.setState({
            dimensions: {
                width: this.container.offsetWidth,
                height: this.container.offsetHeight,
            },
        });
        window.addEventListener('resize', this.handleResize);
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.liveData.isUpdate) {
            // console.log(this.props.liveData)
            let event = JSON.parse(this.props.liveData.data)
            this.updateLiveEvent(event);
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    handleResize(){
        this.setState({dimensions : {width: this.container.offsetWidth, height: this.container.offsetHeight}});
    }

    updateLiveEvent(liveData) {
        if (liveData.type === "location") {
            if (liveData.data.type === "PLAYER") {
                this.updateEvent(liveData.data);
            } else if (liveData.data.type === "BALL") {
                this.updateBall(liveData.data);
            }
        } else if (liveData.type === "stats") {

        } else if (liveData.type === "shot") {

        } else if (liveData.type === "marker") {

        } else if (liveData === "clock") {

        }
    }

    updateEvent(lastEvent) {
        
        if (this.pgRef.current.props.player.id === lastEvent.id) {
            let pg = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.pgRef.current.updateLocation(pg[0], pg[1], playerImage);      
        } else if (this.cRef.current.props.player.id === lastEvent.id) {
            let c = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.cRef.current.updateLocation(c[0], c[1]); 
        } else if (this.sgRef.current.props.player.id === lastEvent.id) {
            let sg = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.sgRef.current.updateLocation(sg[0], sg[1]);
        } else if (this.pfRef.current.props.player.id === lastEvent.id) {
            let pf = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.pfRef.current.updateLocation(pf[0], pf[1]); 
        } else if (this.sfRef.current.props.player.id === lastEvent.id) {
            let sf = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.sfRef.current.updateLocation(sf[0], sf[1]); 
        } else if (this.opgRef.current.props.player.id === lastEvent.id) {
            let pg = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.opgRef.current.updateLocation(pg[0], pg[1]); 
        } else if (this.ocRef.current.props.player.id === lastEvent.id) {
            let c = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.ocRef.current.updateLocation(c[0], c[1]); 
        } else if (this.osgRef.current.props.player.id === lastEvent.id) {
            let sg = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.osgRef.current.updateLocation(sg[0], sg[1]); 
        } else if (this.opfRef.current.props.player.id === lastEvent.id) {
            let pf = this.convertLocToPx(lastEvent.x, lastEvent.y);
            this.opfRef.current.updateLocation(pf[0], pf[1]); 
        } else if (this.osfRef.current.props.player.id === lastEvent.id) {
            // let sf = this.convertLocToPx(lastEvent.x, lastEvent.y);
            // this.osfRef.current.updateLocation(sf[0], sf[1]); 
        }
    }

    updateBall(event) {
        var ball = this.convertLocToPx(event.x, event.y);
        this.ballRef.current.updateLocation(ball[0], ball[1]);
    }

    convertLocToPx(locX, locY) {

    /*
    * y = 28650 mm (-14325, 14325)
    * x = 15240 mm (-7620, 7620)
    */
        const {dimensions} = this.state;
        //Court unit size x : [-250, 250], y : [-80, 840]
        var x_rate = (dimensions.width / 4) / (7620 + 700); // out side border blue area
        var y_rate = (dimensions.width / 2) / (14325 + 1432); // out side border blue area
        var x = x_rate * locX; // for origin zero point for locY ()
        var y = y_rate * locY;

        return [y, x]; // draw on pixi view x = width direction
    }

    convertPercentPointToPix(xp, yp) {
        const { dimensions } = this.state;
        var length_rate = (dimensions.width / 2) / 100;
        var width_rate = length_rate / 2;
        var x = length_rate * xp;
        var y = width_rate * yp;
        return [x, y];
    }
    
    renderContent() {
        const { dimensions, locpg, locc, locsg, locpf, locsf, olocpg, olocc, olocsg, olocpf, olocsf} = this.state;
        
        var pg = locpg; 
        var c =  locc;
        var sg =locsg;
        var pf = locpf;
        var sf = locsf;

        var opg = olocpg; 
        var oc =  olocc;
        var osg =olocsg;
        var opf = olocpf;
        var osf = olocsf;

        if (locpg[0] === 0 && locpg[1] === 0) {pg=this.convertPercentPointToPix(30, 0)}
        if (locc[0] === 0 && locc[1] === 0) {c=this.convertPercentPointToPix(66, 33)}
        if (locsg[0] === 0 && locsg[1] === 0) {sg=this.convertPercentPointToPix(35, -45)}
        if (locpf[0] === 0 && locpf[1] === 0) {pf=this.convertPercentPointToPix(75, -40)}
        if (locsf[0] === 0 && locsf[1] === 0) {sf=this.convertPercentPointToPix(80, 50)}

        if (olocpg[0] === 0 && olocpg[1] === 0) {opg=this.convertPercentPointToPix(-30, 0)}
        if (olocc[0] === 0 && olocc[1] === 0) {oc=this.convertPercentPointToPix(-66, -33)}
        if (olocsg[0] === 0 && olocsg[1] === 0) {osg=this.convertPercentPointToPix(-35, 45)}
        if (olocpf[0] === 0 && olocpf[1] === 0) {opf=this.convertPercentPointToPix(-75, 40)}
        if (olocsf[0] === 0 && olocsf[1] === 0) {osf=this.convertPercentPointToPix(-80, -50)}
        
        var width = dimensions.width;
        var height = dimensions.width / 2;

        if (!this.props.subscribe.isSet) return;

        return (
            <>
            <div className="playground-container" >
            <Stage width={width} height={height} options={{ backgroundColor: 0xffffff, autoDensity:true, forceCanvas:false, sharedLoader:true }} >
                <Container position={[width / 2, height / 2]} width={width} height={height}>
                    <Sprite  anchor={0} x={-width / 2} y={-height / 2} width={width} height={width / 2} image={courtImage} />
                    <AppConsumer>
                        {app => <PlayerIconView className="pg" image={playerImage} player={this.props.subscribe.response.teams[1].players[0]} 
                        number={this.props.shottracker.shottracker.point_guard.number} x={pg[0]} y={pg[1]} ref={this.pgRef} app={app}/>}</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="c" image={playerImage} player={this.props.subscribe.response.teams[1].players[5]} 
                        number={this.props.shottracker.shottracker.center.number} x={c[0]} y={c[1]} ref={this.cRef} app={app}/> }</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="sg" image={playerImage} player={this.props.subscribe.response.teams[1].players[2]} 
                        number={this.props.shottracker.shottracker.shooting_guard.number} x={sg[0]} y={sg[1]} ref={this.sgRef} app={app}/>}</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="pf" image={playerImage} player={this.props.subscribe.response.teams[1].players[3]} 
                        number={this.props.shottracker.shottracker.power_forward.number} x={pf[0]} y={pf[1]} ref={this.pfRef} app={app}/>}</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="sf" image={playerImage} player={this.props.subscribe.response.teams[1].players[4]} 
                        number={this.props.shottracker.shottracker.small_forward.number} x={sf[0]} y={sf[1]} ref={this.sfRef} app={app}/>}</AppConsumer>

                    <AppConsumer>
                        {app => <PlayerIconView className="opg" image={opPlayerImage} player={this.props.subscribe.response.teams[0].players[0]} number={1} x={opg[0]} y={opg[1]} ref={this.opgRef} app={app}/>}</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="oc" image={opPlayerImage} player={this.props.subscribe.response.teams[0].players[1]} number={2} x={oc[0]} y={oc[1]} ref={this.ocRef} app={app}/> }</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="osg" image={opPlayerImage} player={this.props.subscribe.response.teams[0].players[2]} number={3} x={osg[0]} y={osg[1]} ref={this.osgRef} app={app}/>}</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="opf" image={opPlayerImage} player={this.props.subscribe.response.teams[0].players[3]} number={4} x={opf[0]} y={opf[1]} ref={this.opfRef} app={app}/>}</AppConsumer>
                    <AppConsumer>
                        {app => <PlayerIconView className="osf" image={opPlayerImage} player={this.props.subscribe.response.teams[0].players[4]} number={5} x={osf[0]} y={osf[1]} ref={this.osfRef} app={app}/>}</AppConsumer>

                    <AppConsumer>
                        {app => <PlayerIconView x={0 + 5} y={0 + 5} number={""} image={ballIcon} ref={this.ballRef} app={app}/>}</AppConsumer>
                </Container>
            </Stage>
            </div>
            </>
        );
    }
    
      render() {
        const { dimensions } = this.state;
    
        return (
          <div className="court-webgl-bg" ref={el => (this.container = el)}>
            {dimensions && this.renderContent()}
          </div>
        );
      }
}
const mapStateToProps = (state) => {
    const { liveData, subscribeToken, shottracker} = state;
    return{
        liveData: liveData,
        subscribe: subscribeToken,
        shottracker
    }
}

export default connect(mapStateToProps, null, null, {forwardRef:true})(STCourtView);