import actionConstants from '../constants/ActionConstants';

const initialState = {
    error: '',
    shottracker: {},
    isFetching: false,
    isSuccess: false,
  };
  
  export default function shottracker(state = initialState, action) {
    switch (action.type) {
      case actionConstants.GET_ST_REQUEST:
        return {
          ...state,
          error: '',
          isFetching: true,
          isSuccess: false,
        };
      case actionConstants.GET_ST_SUCCESS:
        return {
          ...state,
          shottracker: action.payload,
          isFetching: false,
          isSuccess: true,
        };
      case actionConstants.GET_ST_FAILURE:
        return {
          ...state,
          error: action.message,
          isFetching: false,
        };
      case actionConstants.GET_ST_RESET:
        return {
          ...state,
          error: '',
          isSuccess: false,
          isFetching: false,
        };
      case actionConstants.RESET:
        return {
          ...initialState,
        };
      default:
        return state;
    }
  }