import React from 'react';
import PropTypes from 'prop-types';
import config from '../../config';
import './player.scss';
import placeHolder from '../../assets/male-placeholder.png';


const { API } = config;

function STPlayer(props) {
  const { details, pos} = props;
  const {
    first_name : fn,
    last_name: ln,
    number: num,
    player_id: pid,
  } = details;

  function getPlaceHolder() {
      return placeHolder;
  }

  return (
    <div className="players-details-wrap">
      <div className="players-image">
        <img src={`${API.baseImagesUrl}${pid}.png`} alt={`${fn} ${ln}`} onError={getPlaceHolder}/>
      </div>
      <div className="players-details">
        <div className="players-score-head">
          <div className="score">{num}</div>
          <div className="player-position">{pos}</div>
        </div>
        <div className="player-name">{`${fn.toUpperCase()}`}</div>
        <div className="player-name">{`${ln.toUpperCase()}`}</div>
        <div className="players-score-wrap">
          <ul className="players-score-ul">
            <li className="score-li">
              <span>PTS</span>
              <span>{0}</span>
            </li>
            <li className="score-li">
              <span>REB</span>
              <span>{0}</span>
            </li>
            <li className="score-li">
              <span>AST</span>
              <span>{0}</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

STPlayer.propTypes = {
  details: PropTypes.object.isRequired,
};

export default STPlayer;
