import actionConstants from '../constants/ActionConstants';

const initialState = {
    product: null,
    isUpdate: false,
  };
  
export default function arviewProduct(state = initialState, action) {
    switch (action.type) {
      case actionConstants.UPDATE_AR_VIEW_PRODUCT:
        return {
          ...state,
          product : action.product,
          isUpdate: true
        };
      case actionConstants.RESET_AR_VIEW_PRODUCT:
        return{
            ...state,
            isUpdate : false
        }
      default:
        return state;
    }
}