const positionOrder = {
    "PG": 1,
    "C": 2,
    "SG": 3,
    "PF": 4,
    "SF": 5
};

const sortPlayers = ( players ) => {
  const list = players.sort((a, b) => positionOrder[a.pos] < positionOrder[b.pos] ? -1 : (positionOrder[a.pos] > positionOrder[b.pos] ? 1 : 0))
  return list;
};

export { sortPlayers } ;