import actionConstants from '../constants/ActionConstants';

const initialState = {
  error: '',
  teamSchedule: {},
  isFetching: false,
  isSuccess: false,
};

export default function teamSchedule(state = initialState, action) {
  switch (action.type) {
    case actionConstants.GET_TEAM_SCHEDULE_REQUEST:
      return {
        ...state,
        error: '',
        isFetching: true,
        isSuccess: false,
      };
    case actionConstants.GET_TEAM_SCHEDULE_SUCCESS:
      return {
        ...state,
        teamSchedule: action.payload,
        isFetching: false,
        isSuccess: true,
      };
    case actionConstants.GET_TEAM_SCHEDULE_FAILURE:
      return {
        ...state,
        error: action.message,
        isFetching: false,
      };
    case actionConstants.GET_TEAM_SCHEDULE_RESET:
      return {
        ...state,
        error: '',
        isSuccess: false,
        isFetching: false,
      };
    case actionConstants.RESET:
      return {
        ...initialState,
      };
    default:
      return state;
  }
}
