import actionConstants from '../constants/ActionConstants';

const initialState = {
    response: null,
    isSet: false,
  };
  
export default function subscribeToken(state = initialState, action) {
    switch (action.type) {
      case actionConstants.UPDATE_SUBSCRIBE_TOKEN:
        return {
          ...state,
          response : action.response,
          isSet: true
        };
      case actionConstants.RESET_SUBSCRIBE_TOKEN:
        return{
            ...state,
            isSet : false
        }
      default:
        return state;
    }
}